<template>
  <div class="bodyContent" v-if="s2_pd_detail">
    <!-- Dialog (Modal) FDA Search progress -->
    <v-dialog v-model="show_modalBox_fdaSearch" persistent max-width="1000">
      <v-card align="center" :loading="fdaSearch_loading">
        <div v-if="fdaSearch_loading">
          <v-icon align="center" color="grey lighten-3" size="80" class="ma-6">
            fa-search
          </v-icon>
          <p>กำลังทำการค้นหาจากฐานข้อมูล อย.</p>
          <!-- <p>ตัวอย่างในการค้นหา</p>
          <span>ผลิตภัณฑ์อาหาร เลขใบสำคัญ 10-3-XXXXX-X-XXXX หรือ ค้นหาด้วยชื่อ  "ขนมแสนอร่อย"</span><br>
          <span>ผลิตภัณฑ์ยา เลขใบสำคัญ G1/59 หรือ ค้นหาด้วยชื่อการค้าเช่น "ยาตราเมดิคอล"</span><br>
          <span>ผลิตภัณฑ์วัตุอันตราย เลขใบสำคัญ 1/2559 หรือ ค้นหาด้วยชื่อ "ยาฉีดปลวก"</span><br>
          <span>ผลิตภัณฑ์เครื่องสำอาง 10-X-XXXXXXX หรือ ค้นหาด้วยชื่อ "แป้งแป้ง"</span><br>
          <span>ผลิตภัณฑ์ยาเสพติด 23 10/47 หรือ ค้นหาด้วยชื่อ "ทรานส์"</span><br>
          <span>ผลิตภัณฑ์เครื่องมือแพทย์ ผ.1/2559 หรือ ค้นหาด้วยชื่อ "เครื่องบดกระดูก"</span><br>
          <span>สถานที่ยา ค้นหาด้วยชื่อเช่น "ฟาร์มาซีเภสัช"</span><br> -->
        </div>

        <div v-else>
          <div v-if="fdaSearch_result">
            <v-icon
              align="center"
              color="green darken-1"
              size="80"
              class="ma-6"
            >
              fa-search
            </v-icon>
            <p class="text_title_success pt-0">
              พบ {{ temp_fdaInfo.length }} รายการข้อมูลที่ค้นหา
            </p>
            <div class="text-left mx-6">
              <v-row align="center" justify="center">
                <v-col
                  cols="6"
                  md="6"
                  v-for="(content, i) in temp_fdaInfo"
                  :key="i"
                >
                  <v-card class="pa-4">
                    <p class="text_title_question my-0">
                      รายการที่ {{ i + 1 }}
                    </p>
                    <br />
                    <span class="text_fda_header">หมายเลข อย.</span><br />
                    <span>{{ content.FDA_license }}</span
                    ><br />

                    <v-divider class="my-4"></v-divider>

                    <span class="text_fda_header">สถานะการขึ้นทะเบียน</span
                    ><br />
                    <span>{{ content.FDA_status }}</span
                    ><br />

                    <v-divider class="my-4"></v-divider>

                    <span class="text_fda_header">ประเภทผลิตภัณฑ์</span><br />
                    <span>{{ content.FDA_type }}</span
                    ><br />

                    <v-divider class="my-4"></v-divider>

                    <span class="text_fda_header">ชื่อทางการค้า (ภาษาไทย)</span
                    ><br />
                    <span>{{ content.FDA_nameTH }}</span
                    ><br />

                    <v-divider class="my-4"></v-divider>

                    <span class="text_fda_header"
                      >ชื่อทางการค้า (ภาษาอังกฤษ)</span
                    ><br />
                    <span>{{ content.FDA_nameENG }}</span
                    ><br />

                    <v-divider class="my-4"></v-divider>

                    <span class="text_fda_header">ชื่อผู้ผลิต</span><br />
                    <span>{{ content.FDA_owner }}</span
                    ><br />

                    <v-divider class="my-4"></v-divider>

                    <span class="text_fda_header"
                      >Newcode (เลขอ้างอิงใบรับแจ้งการนำเข้าผลิตภัณฑ์)</span
                    ><br />
                    <span>{{ content.FDA_newcode }}</span
                    ><br />

                    <v-divider class="my-4"></v-divider>

                    <span class="text_fda_header">ชื่อสถานที่ผลิต</span><br />
                    <span>{{ content.FDA_manufact }}</span
                    ><br />

                    <v-divider class="my-4"></v-divider>

                    <span class="text_fda_header">ที่อยู่ผู้ผลิต</span><br />
                    <span>{{ content.FDA_manufactLocation }}</span
                    ><br />

                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn
                        depressed
                        rounded
                        width="50%"
                        dark
                        color="green lighten-1"
                        @click="fn_match_FDAinfo(i)"
                        class="ma-4"
                        >ตรง</v-btn
                      >
                    </v-card-actions>
                  </v-card>
                </v-col>
              </v-row>
            </div>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                depressed
                rounded
                large
                text
                width="25%"
                color="red darken-1"
                @click="show_modalBox_fdaSearch = false"
                class="ma-4"
                >ไม่ตรง</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </div>

          <div v-else>
            <v-icon align="center" color="red darken-3" size="80" class="ma-6">
              fa-exclamation-triangle
            </v-icon>
            <p class="text_title_alert">ขออภัย ไม่พบรายการที่ค้นหา</p>
            <v-card-text align="left"
              >ไม่พบข้อมูลจากหมายเลขที่ท่านค้นหาหรือหมายเลขที่กรอกไม่ถูกต้อง</v-card-text
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                depressed
                rounded
                large
                text
                color="red darken-1"
                @click="show_modalBox_fdaSearch = false"
                >รับทราบ</v-btn
              >
            </v-card-actions>
          </div>
        </div>
      </v-card>
    </v-dialog>

    <!-- Dialog (Modal) Add Drug -->
    <v-dialog v-model="show_modalBox_drugSet" persistent max-width="600">
      <v-card align="center">
        <div v-if="s2_pd_detail.pd_pdTypeIndex - 1 == 2" class="pa-6">
          <v-icon align="center" color="#00B5F8" size="80" class="ma-6">
            fa-seedling
          </v-icon>
          <p>เพิ่มข้อมูลชนิดของสมุนไพร</p>
          <v-row align="center" justify="center">
            <v-col cols="12" class="py-0">
              <v-file-input
                outlined
                rounded
                v-model="s2_temp_drugImg"
                show-size
                counter
                prepend-inner-icon="$file"
                prepend-icon=""
                accept="image/*"
                label="คลิกเพื่อเพิ่มรูปภาพ"
              >
                <template v-slot:selection="{ text }">
                  <v-chip color="primary">
                    {{ text }}
                  </v-chip>
                </template>
              </v-file-input>
            </v-col>
          </v-row>
          <v-row align="center" justify="center">
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                label="ชื่อสมุนไพร"
                outlined
                rounded
                v-model="s2_temp_drugName"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6" class="py-0">
              <v-select
                label="เลือกส่วนของสมุนไพร"
                :items="list_productHerbPart"
                outlined
                rounded
                v-model="s2_temp_drugAsset"
              ></v-select>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="6" class="py-0" />
            <v-col
              v-if="s2_temp_drugAsset == 'อื่น ๆ'"
              cols="12"
              md="6"
              align="left"
              class="py-0"
            >
              <v-text-field
                label="กรอกรายละเอียด"
                outlined
                auto-grow
                rounded
                v-model="s2_temp_drugInfo"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              depressed
              rounded
              width="25%"
              dark
              color="grey lighten-2"
              @click="show_modalBox_drugSet = false"
              class="ma-4"
              >ยกเลิก</v-btn
            >
            <v-btn
              depressed
              rounded
              width="25%"
              dark
              color="#00B5F8"
              @click="addDrugSet"
              class="ma-4"
              >เพิ่ม</v-btn
            >
          </v-card-actions>
        </div>

        <div v-if="s2_pd_detail.pd_pdTypeIndex - 1 == 4" class="pa-6">
          <v-icon align="center" color="#00B5F8" size="80" class="ma-6">
            fa-prescription-bottle
          </v-icon>
          <p>เพิ่มข้อมูลชนิดของยา</p>
          <v-row align="center" justify="center">
            <v-col cols="12" md="6" class="py-0">
              <v-file-input
                outlined
                rounded
                v-model="s2_temp_drugImg"
                show-size
                counter
                prepend-inner-icon="$file"
                prepend-icon=""
                accept="image/*"
                label="คลิกเพื่อเพิ่มรูปภาพ"
              >
                <template v-slot:selection="{ text }">
                  <v-chip color="primary">
                    {{ text }}
                  </v-chip>
                </template>
              </v-file-input>
            </v-col>
          </v-row>
          <v-row align="center" justify="center">
            <v-col cols="12" md="6" class="py-0">
              <v-checkbox
                class="py-0 mb-6"
                v-model="s2_temp_knownDrug"
                label="ท่านรู้จักยาชนิดนี้หรือไม่"
              >
              </v-checkbox>
            </v-col>
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                label="ชื่อยา"
                outlined
                rounded
                v-model="s2_temp_drugName"
                :disabled="!s2_temp_knownDrug"
                :filled="!s2_temp_knownDrug"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              depressed
              rounded
              width="25%"
              dark
              color="grey lighten-2"
              @click="show_modalBox_drugSet = false"
              class="ma-4"
              >ยกเลิก</v-btn
            >
            <v-btn
              depressed
              rounded
              width="25%"
              dark
              color="#00B5F8"
              @click="addDrugSet"
              class="ma-4"
              >เพิ่ม</v-btn
            >
          </v-card-actions>
        </div>
      </v-card>
    </v-dialog>

    <!-- Dialog (Modal) Add Testkit -->
    <v-dialog v-model="show_modalBox_testkit" persistent max-width="600">
      <v-card align="center">
        <div class="pa-6">
          <v-icon align="center" color="#00B5F8" size="80" class="ma-6">
            fa-eye-dropper
          </v-icon>
          <p>เพิ่มข้อมูลชุดทดสอบอย่างง่าย (Test-kit)</p>
          <v-row align="center" justify="center">
            <v-col cols="12" class="py-0">
              <v-file-input
                outlined
                rounded
                v-model="s2_temp_tk_img"
                show-size
                counter
                prepend-inner-icon="$file"
                prepend-icon=""
                accept="image/*"
                label="คลิกเพื่อเพิ่มรูปภาพ"
              >
                <template v-slot:selection="{ text }">
                  <v-chip color="primary">
                    {{ text }}
                  </v-chip>
                </template>
              </v-file-input>
            </v-col>
          </v-row>
          <v-row align="center" justify="center">
            <v-col cols="12" md="6" class="mt-4">
              <v-select
                label="เลือกส่วนชุดทดสอบ"
                :items="list_testkit"
                @change="getTestkitINDEX"
                outlined
                rounded
                v-model="s2_temp_tk_name"
              ></v-select>
            </v-col>
            <v-col cols="12" md="6" class="py-0" align="left">
              <span>รายละเอียดการตรวจสอบ</span>
              <v-radio-group v-model="s2_temp_tk_isPositive" row class="py-0">
                <v-radio label="ไม่พบสารปนเปื้อน" value="0"></v-radio>
                <v-radio label="พบสารปนเปื้อน" value="1"></v-radio>
              </v-radio-group>
            </v-col>
          </v-row>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              depressed
              rounded
              width="25%"
              dark
              color="grey lighten-2"
              @click="show_modalBox_testkit = false"
              class="ma-4"
              >ยกเลิก</v-btn
            >
            <v-btn
              depressed
              rounded
              width="25%"
              dark
              color="#00B5F8"
              @click="addTestkit"
              class="ma-4"
              >เพิ่ม</v-btn
            >
          </v-card-actions>
        </div>
      </v-card>
    </v-dialog>

    <!-- Content forms -->
    <v-row>
      <v-col cols="12" justify="center" align="start">
        <p class="header_form px-6">
          ข้อมูลผลิตภัณฑ์ที่ต้องสงสัย <br />(4 ขั้นตอน)
        </p>
        <div>
          <v-stepper vertical class="elevation-0" v-model="compStep">
            <template v-for="n in steps">
              <!-- Setup stepper detail -->
              <v-stepper-step
                :key="`${n}-step`"
                :complete="compStep > n"
                :step="n"
              >
                {{ stepName[n - 1] }}
              </v-stepper-step>

              <!-- Item in stepper -->
              <v-stepper-content :key="`${n}-content`" :step="n">
                <!-- Type of Product and photo(s) -->
                <div v-if="n == 1">
                  <!-- Select ProductType -->
                  <v-row justify="start" class="pr-2">
                    <v-col cols="12" md="6" align="left">
                      <v-form ref="form" v-model="valid_step1" lazy-validation>
                        <v-select
                          label="เลือกประเภทผลิตภัณฑ์"
                          :items="list_productType"
                          @change="getPDTypeINDEX"
                          outlined
                          rounded
                          v-model="s2_pd_detail.pd_pdTypeName"
                          required
                          :error-messages="productTypeErrors"
                          @input="$v.s2_pd_detail.pd_pdTypeName.$touch()"
                          @blur="$v.s2_pd_detail.pd_pdTypeName.$touch()"
                        ></v-select>
                      </v-form>
                    </v-col>
                    <!-- ProductType Description -->
                    <v-col cols="12" md="6" align="left">
                      <div
                        v-if="s2_pd_detail.pd_pdTypeName != ''"
                        class="subtitle"
                      >
                        <v-row class="pa-2">
                          <v-col cols="1" class="pt-1">
                            <v-avatar slot="icon" color="#00B5F8" size="20">
                              <v-icon x-small color="white">
                                {{
                                  list_pdTypeIcon[
                                    s2_pd_detail.pd_pdTypeIndex - 1
                                  ]
                                }}
                              </v-icon>
                            </v-avatar>
                          </v-col>
                          <v-col cols="11" class="px-1 py-1">
                            <span class="subtitle_bold">
                              {{ s2_pd_detail.pd_pdTypeName }}
                            </span>
                            <span class="subtitle">
                              คือ
                              {{
                                list_productDescription[
                                  s2_pd_detail.pd_pdTypeIndex - 1
                                ]
                              }}</span
                            >
                          </v-col>
                        </v-row>
                      </div>
                      <span v-else class="subtitle">
                        กรุณาระบุประเภทของผลิตภัณฑ์ที่พบ
                      </span>
                    </v-col>
                  </v-row>

                  <!-- Photo(s) -->
                  <v-row justify="center" class="pr-2">
                    <v-col cols="12" class="pt-5" align="left">
                      <div>
                        <span class="mr-6"
                          >รูปภาพผลิตภัณฑ์ต้องสงสัย <br />
                        </span>
                        <span v-if="!imgStatus" class="text_status_imgFailed"
                          >(สถานะ ยังไม่บันทึก) <br
                        /></span>
                        <span v-else class="text_status_imgSuccess"
                          >(สถานะ บันทึกสำเร็จ) <br
                        /></span>
                        <span class="text_warning_danger">
                          ข้อควรระวัง: รูปภาพจะไม่ถูกบันทึก
                          หากไม่กดปุ่มบันทึกรูปภาพ <br
                        /></span>
                        <v-alert
                          dismissible
                          transition="scroll-x-transition"
                          v-if="imgNotfound"
                          type="error"
                          class="mt-5"
                        >
                          กรุณาเลือกรูปภาพอย่างน้อยจำนวน 1 รูปภาพ
                        </v-alert>
                        <v-row>
                          <v-col cols="12">
                            <v-file-input
                              outlined
                              rounded
                              v-model="s2_pd_photos"
                              show-size
                              counter
                              multiple
                              prepend-inner-icon="$file"
                              prepend-icon=""
                              accept="image/*"
                              label="คลิกเพื่อเพิ่มรูปภาพ"
                            >
                              <template v-slot:selection="{ text }">
                                <v-chip color="primary">
                                  {{ text }}
                                </v-chip>
                              </template>
                            </v-file-input>
                          </v-col>
                          <v-col
                            cols="12"
                            md="6"
                            class="mt-1"
                            justify="center"
                            align="center"
                          >
                            <v-btn
                              depressed
                              block
                              rounded
                              large
                              color="primary"
                              @click="uploadIMG"
                              >บันทึกรูปภาพ</v-btn
                            >
                          </v-col>
                        </v-row>
                        <v-card outlined justify="center" align="center">
                          <div
                            v-if="pd_photo_show.length == 0"
                            justify="center"
                            align="center"
                            class="pa-5"
                          >
                            <v-icon class="py-5" size="130" color="#CDCDCD"
                              >fa-images</v-icon
                            >
                            <p>
                              พื้นที่แสดงรูปภาพผลิตภัณฑ์
                              (รูปภาพจะปรากฎเมื่อทำการอัปโหลดสำเร็จ)
                            </p>
                            <span class="subtitle"
                              >การอัปโหลดรูปภาพมายัง Tawai for Health</span
                            >
                            <br />
                            <span class="subtitle"
                              >ข้าพเจ้ายอมรับ ข้อตกลง Terms of Use and Privacy
                              Policy ของทาง TaWai</span
                            >
                          </div>
                          <div v-else class="pa-10">
                            <v-row>
                              <v-col
                                v-for="(n, index) in pd_photo_show"
                                :key="index"
                                class="d-flex child-flex"
                                cols="6"
                              >
                                <v-img
                                  :src="n"
                                  contain
                                  aspect-ratio="1.3"
                                  class="grey lighten-2"
                                >
                                </v-img>
                              </v-col>
                            </v-row>
                          </div>
                        </v-card>
                      </div>
                    </v-col>
                  </v-row>

                  <!-- Button onNextStep or Cancel -->
                  <v-row justify="center">
                    <v-btn
                      depressed
                      rounded
                      large
                      class="ma-8"
                      width="25%"
                      color="gray"
                      @click="backStep(n)"
                      >ย้อนกลับ</v-btn
                    >
                    <v-btn
                      depressed
                      rounded
                      large
                      class="ma-8"
                      width="25%"
                      color="primary"
                      @click="nextStep(n)"
                      :disabled="!valid_step1"
                      >ถัดไป</v-btn
                    >
                  </v-row>
                </div>

                <!-- License and Product Detail -->
                <div v-else-if="n == 2">
                  <!-- Product license and label information -->
                  <v-row justify="start" class="mt-4">
                    <v-col cols="12" align="left" class="pa-0 mx-4">
                      <p class="header_form">ข้อมูลทะเบียนผลิตภัณฑ์</p>
                    </v-col>
                  </v-row>
                  <!-- Product Label check -->
                  <v-col cols="12" class="py-0">
                    <v-checkbox
                      class="py-0"
                      v-model="s2_pd_label.pd_hasLabel"
                      label="ท่านพบฉลากภาษาไทยหรือไม่"
                      @change="fn_s2_checkHasLabel"
                    >
                    </v-checkbox>
                  </v-col>

                  <!-- Product License check and information -->
                  <div
                    v-if="
                      s2_pd_detail.pd_pdTypeIndex - 1 == 2 ||
                        s2_pd_detail.pd_pdTypeIndex - 1 == 4
                    "
                  >
                    <!-- Something -->
                  </div>

                  <!-- Product license of pdType = 3 || 5 (manufacturer detailed) -->
                  <div v-else>
                    <v-col cols="12" class="py-0">
                      <v-checkbox
                        class="py-0"
                        v-model="s2_pd_label.pd_hasLicense"
                        label="มีการขึ้นทะเบียนผลิตภัณฑ์ อย. หรือไม่"
                        @change="fn_s2_checkHasLicense"
                        :disabled="!s2_pd_label.pd_hasLabel"
                      >
                      </v-checkbox>
                    </v-col>
                    <!-- License information -->
                    <v-row class="pt-2" align="center">
