                    </v-row>
                  </div>

                  <!-- Button onNextStep or Cancel -->
                  <v-row justify="center">
                    <v-btn
                      depressed
                      rounded
                      large
                      class="ma-8"
                      width="25%"
                      color="gray"
                      @click="backStep(n)"
                      >ย้อนกลับ</v-btn
                    >
                    <v-btn
                      depressed
                      rounded
                      large
                      class="ma-8"
                      width="25%"
                      color="primary"
                      @click="nextStep(n)"
                      >ถัดไป</v-btn
                    >
                  </v-row>
                </div>

                <!-- EXTRACHECK -->
                <div v-else>
                  <!-- Extracheck Header -->
                  <v-row justify="start" class="mt-4">
                    <v-col cols="12" align="left" class="pa-0 mx-4">
                      <p class="header_form">การตรวจสอบเพิ่มเติม</p>
                    </v-col>
                  </v-row>

                  <!-- Detail -->
                  <v-row
                    v-if="
                      s2_pd_detail.pd_pdTypeIndex == 1 ||
                        s2_pd_detail.pd_pdTypeIndex == 2 ||
                        s2_pd_detail.pd_pdTypeIndex == 4 ||
                        s2_pd_detail.pd_pdTypeIndex == 5 ||
                        s2_pd_detail.pd_pdTypeIndex == 7 ||
                        s2_pd_detail.pd_pdTypeIndex == 8 ||
                        s2_pd_detail.pd_pdTypeIndex == 9
                    "
                    class="mx-6 pa-0"
                    align="center"
                  >
                    <v-checkbox
                      class="py-0 mr-6"
                      v-model="s2_pd_check.isCheckDMSC"
                      label="ตรวจสอบจากฐานข้อมูล กรมวิทย์ with you (alert.dmsc.moph.go.th) "
                    >
                    </v-checkbox>
                    <a
                      class="pb-2"
                      href="http://alert.dmsc.moph.go.th/"
                      target="_blank"
                      >ไปที่เว็บไซต์กรมวิทย์ With You</a
                    >
                  </v-row>
                  <v-row class="mx-12" v-if="s2_pd_check.isCheckDMSC">
                    <v-col cols="12" md="8" class="py-0">
                      <v-radio-group
                        v-model="s2_pd_checkInfo.check_DMSC_status"
                        row
                      >
                        <v-radio label="ไม่พบ" value="0"></v-radio>
                        <v-radio label="พบ" value="1"></v-radio>
                      </v-radio-group>
                    </v-col>
                    <v-col
                      v-if="parseInt(s2_pd_checkInfo.check_DMSC_status)"
                      cols="12"
                      md="8"
                      class="py-0"
                    >
                      <v-text-field
                        label="กรอกรายละเอียด"
                        outlined
                        rounded
                        auto-grow
                        v-model="s2_pd_checkInfo.check_DMSC_info"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-divider
                    v-if="
                      s2_pd_detail.pd_pdTypeIndex == 1 ||
                        s2_pd_detail.pd_pdTypeIndex == 2 ||
                        s2_pd_detail.pd_pdTypeIndex == 4 ||
                        s2_pd_detail.pd_pdTypeIndex == 5 ||
                        s2_pd_detail.pd_pdTypeIndex == 7 ||
                        s2_pd_detail.pd_pdTypeIndex == 8 ||
                        s2_pd_detail.pd_pdTypeIndex == 9
                    "
                  >
                  </v-divider>

                  <v-row
                    v-if="
                      s2_pd_detail.pd_pdTypeIndex == 1 ||
                        s2_pd_detail.pd_pdTypeIndex == 2 ||
                        s2_pd_detail.pd_pdTypeIndex == 4 ||
                        s2_pd_detail.pd_pdTypeIndex == 6 ||
                        s2_pd_detail.pd_pdTypeIndex == 7 ||
                        s2_pd_detail.pd_pdTypeIndex == 8 ||
                        s2_pd_detail.pd_pdTypeIndex == 9 ||
                        s2_pd_detail.pd_pdTypeIndex == 10
                    "
                    class="mx-6"
                  >
                    <v-checkbox
                      class="py-0"
                      v-model="s2_pd_check.isCheckQuality"
                      label="ตรวจสอบผลิตภัณฑ์เสื่อมคุณภาพ"
                    >
                    </v-checkbox>
                  </v-row>
                  <v-row class="mx-12" v-if="s2_pd_check.isCheckQuality">
                    <v-col cols="12" md="8" class="py-0">
                      <v-radio-group
                        v-model="s2_pd_checkInfo.check_quality_status"
                        row
                      >
                        <v-radio label="ไม่เสื่อมคุณภาพ" value="0"></v-radio>
                        <v-radio label="เสื่อมคุณภาพ" value="1"></v-radio>
                      </v-radio-group>
                    </v-col>
                    <v-col
                      v-if="parseInt(s2_pd_checkInfo.check_quality_status)"
                      cols="12"
                      md="8"
                      class="py-0"
                    >
                      <v-text-field
                        label="กรอกรายละเอียด"
                        outlined
                        auto-grow
                        rounded
                        v-model="s2_pd_checkInfo.check_quality_info"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-divider
                    v-if="
                      s2_pd_detail.pd_pdTypeIndex == 1 ||
                        s2_pd_detail.pd_pdTypeIndex == 2 ||
                        s2_pd_detail.pd_pdTypeIndex == 4 ||
                        s2_pd_detail.pd_pdTypeIndex == 6 ||
                        s2_pd_detail.pd_pdTypeIndex == 7 ||
                        s2_pd_detail.pd_pdTypeIndex == 8 ||
                        s2_pd_detail.pd_pdTypeIndex == 9 ||
                        s2_pd_detail.pd_pdTypeIndex == 10
                    "
                  >
                  </v-divider>

                  <v-row
                    v-if="
                      s2_pd_detail.pd_pdTypeIndex == 1 ||
                        s2_pd_detail.pd_pdTypeIndex == 2 ||
                        s2_pd_detail.pd_pdTypeIndex == 3 ||
                        s2_pd_detail.pd_pdTypeIndex == 4 ||
                        s2_pd_detail.pd_pdTypeIndex == 6 ||
                        s2_pd_detail.pd_pdTypeIndex == 7 ||
                        s2_pd_detail.pd_pdTypeIndex == 8 ||
                        s2_pd_detail.pd_pdTypeIndex == 9 ||
                        s2_pd_detail.pd_pdTypeIndex == 10
                    "
                    class="mx-6"
                  >
                    <v-checkbox
                      class="py-0"
                      v-model="s2_pd_check.isCheckAds"
                      label="ไม่บอกความจริงหรือโอ้อวดสรรพคุณเกินจริง"
                    >
                    </v-checkbox>
                  </v-row>
                  <v-row class="mx-12" v-if="s2_pd_check.isCheckAds">
                    <v-col cols="12" md="8" class="py-0">
                      <v-radio-group
                        v-model="s2_pd_checkInfo.check_ads_status"
                        row
                      >
                        <v-radio label="ไม่โอ้อวดสรรพคุณ" value="0"></v-radio>
                        <v-radio label="โอ้อวดสรรพคุณ" value="1"></v-radio>
                      </v-radio-group>
                    </v-col>
                    <v-col
                      v-if="parseInt(s2_pd_checkInfo.check_ads_status)"
                      cols="12"
                      md="8"
                      class="py-0"
                    >
                      <v-text-field
                        label="กรอกรายละเอียด"
                        outlined
                        rounded
                        auto-grow
                        v-model="s2_pd_checkInfo.check_ads_info"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-divider
                    v-if="
                      s2_pd_detail.pd_pdTypeIndex == 1 ||
                        s2_pd_detail.pd_pdTypeIndex == 2 ||
                        s2_pd_detail.pd_pdTypeIndex == 3 ||
                        s2_pd_detail.pd_pdTypeIndex == 4 ||
                        s2_pd_detail.pd_pdTypeIndex == 6 ||
                        s2_pd_detail.pd_pdTypeIndex == 7 ||
                        s2_pd_detail.pd_pdTypeIndex == 8 ||
                        s2_pd_detail.pd_pdTypeIndex == 9 ||
                        s2_pd_detail.pd_pdTypeIndex == 10
                    "
                  >
                  </v-divider>

                  <v-row
                    v-if="
                      s2_pd_detail.pd_pdTypeIndex == 2 ||
                        s2_pd_detail.pd_pdTypeIndex == 5 ||
                        s2_pd_detail.pd_pdTypeIndex == 7 ||
                        s2_pd_detail.pd_pdTypeIndex == 8 ||
                        s2_pd_detail.pd_pdTypeIndex == 9
                    "
                    class="mx-6"
                  >
                    <v-checkbox
                      class="py-0"
                      v-model="s2_pd_check.isCheckTestkit"
                      label="การตรวจสอบวิเคราะห์ด้วยชุดทดสอบอย่างง่าย (Test-kit)"
                    >
                    </v-checkbox>
                  </v-row>
                  <!-- Testkit list information -->
                  <div v-if="s2_pd_check.isCheckTestkit" class="mx-6">
                    <v-row justify="start" class="mt-4">
                      <v-col cols="12" align="left" class="pa-0 mx-4">
                        <p class="header_form">
                          รายการชุดทดสอบอย่างง่าย (Test-kit)
                        </p>
                      </v-col>
                    </v-row>
                    <v-row
                      v-for="(content, i) in s2_testkit"
                      :key="i"
                      class="mx-6"
                      align="center"
                    >
                      <span class="text_itemNumber mb-8"
                        >รายการที่ {{ i + 1 }}
                      </span>
                      <v-col cols="6" md="4" align="left" class="py-0">
                        <v-text-field
                          class="my-0"
                          label="ชื่อภาพ"
                          outlined
                          rounded
                          v-model="content.imgName"
                          readonly
                        ></v-text-field>
                      </v-col>
                      <v-col cols="6" md="4" align="left" class="py-0">
                        <v-text-field
                          class="my-0"
                          label="ชื่อชุดทดสอบ"
                          outlined
                          rounded
                          v-model="content.name"
                          readonly
                        ></v-text-field>
                      </v-col>
                      <v-col cols="6" md="3" align="left" class="py-0">
                        <v-text-field
                          class="my-0"
                          label="สถานะการปนเปื้อน"
                          outlined
                          rounded
                          v-model="content.isPositiveName"
                          readonly
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" md="1" align="left" class="py-0">
                        <v-icon
                          align="center"
                          color="red darken-1"
                          class="ma-6 pb-8"
                          @click="delTestkit(i)"
                        >
                          fa-trash-alt
                        </v-icon>
                      </v-col>
                    </v-row>
                    <v-btn
                      block
                      depressed
                      rounded
                      large
                      width="25%"
                      color="gray"
                      @click="show_modalBox_testkit = true"
                      >เพิ่มชุดทดสอบ</v-btn
                    >
                    <v-divider class="my-4"></v-divider>
                  </div>

                  <v-divider
                    v-if="
                      (s2_pd_detail.pd_pdTypeIndex == 2 ||
                        s2_pd_detail.pd_pdTypeIndex == 5 ||
                        s2_pd_detail.pd_pdTypeIndex == 7 ||
                        s2_pd_detail.pd_pdTypeIndex == 8 ||
                        s2_pd_detail.pd_pdTypeIndex == 9) &&
                        caseProperties.caseType == 1 &&
                        userProperties.userRole == 3
                    "
                  >
                  </v-divider>

                  <!-- Extra Check Related between product and unsymptom -->
                  <div
                    v-if="
                      caseProperties.caseType == 1 &&
                        userProperties.userRole == 3
                    "
                  >
                    <v-row class="mx-6">
                      <v-checkbox
                        class="py-0"
                        readonly=""
                        v-model="s2_pd_related.isCheck_related"
                        label="ระดับความสัมพันธ์ระหว่างผลิตภัณฑ์กับอาการ"
                      >
                      </v-checkbox>
                    </v-row>
                    <v-row class="mx-12" v-if="s2_pd_related.isCheck_related">
                      <v-col cols="12" md="6" class="py-0">
                        <v-select
                          label="เลือกระดับความสัมพันธ์"
                          :items="list_relatedType"
                          @change="getRelatedINDEX"
                          outlined
                          rounded
                          v-model="s2_pd_related.pd_check_relatedName"
                        ></v-select>
                      </v-col>
                    </v-row>
                  </div>
                  <!-- Button onNextStep or Cancel -->
                  <v-row justify="center">
                    <v-btn
                      depressed
                      rounded
                      large
                      class="ma-8"
                      width="25%"
                      color="gray"
                      @click="backStep(n)"
                      >ย้อนกลับ</v-btn
                    >
                    <v-btn
                      depressed
                      rounded
                      large
                      class="ma-10"
                      width="40%"
                      color="primary"
                      @click="nextStep(n)"
                      >ถัดไป (แหล่งที่มา)</v-btn
                    >
                  </v-row>
                </div>
              </v-stepper-content>
            </template>
          </v-stepper>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";
import moment from "moment";
import EventBus from "../EventBus";
export default {
  mixins: [validationMixin],
  validations: {
    s2_pd_detail: {
      pd_pdTypeName: { required }
    }
  },
  components: {
    //
  },
  props: {
    caseProperties: Object,
    pd_photo_show: Array,
    s2_pd_detail: Object,
    s2_pd_label: Object,
    s2_pd_licenseInfo: Object,
    s2_pd_manufacturer: Object,
    s2_pd_usage: Object,
    s2_pd_check: Object,
    s2_pd_checkInfo: Object,
    s2_drugSet: Array,
    s2_testkit: Array,

    // With default value
    s1_user_info: {
      type: Object,
      default() {
        return {
          us_hasUser: true
        };
      }
    },
    s2_pd_duration: {
      type: Object,
      default() {
        return {
          pd_dur_dateOfUse: "",
          pd_dur_dateOfStop: "",
          pd_dur_contUse: true
        };
      }
    },
    s2_pd_related: {
      type: Object,
      default() {
        return {
          pd_check_relatedID: 4,
          pd_check_relatedName: ""
        };
      }
    }
  },
  watch: {
    checkProps_pdTypeList() {
      // console.log('New Check Productlist');
      // console.log('New props ProductTypeName', this.s2_pd_detail.pd_pdTypeName);
      this.getPDTypeINDEX();
      this.checkIsHasMFdetail();
    },
    checkProps_pdType() {
      // console.log('New props ProductTypeName', this.s2_pd_detail.pd_pdTypeName);
      this.getPDTypeINDEX();
      this.checkIsHasMFdetail();
    }
  },
  data: () => ({
    compStep: 1,
    steps: 4,
    imgNotfound: false,
    imgStatus: false,
    valid_step1: false,
    valid_step2: false,
    show_modalBox_fdaSearch: false,
    fdaSearch_loading: false,
    fdaSearch_result: false,
    stepName: [
      "ประเภทและรูปภาพของผลิตภัณฑ์",
      "ข้อมูลทะเบียนผลิตภัณฑ์",
      "รูปแบบผลิตภัณฑ์และการใช้งาน",
      "การตรวจสอบเพิ่มเติมและชุดทดสอบ"
    ],
    list_productType: [],
    list_productDescription: [],
    list_licenseStatus: [],
    list_productKind: [],
    list_productInstruction: [],
    list_productKind_ID: [],
    list_productInstruction_ID: [],
    list_productHerbPart: [],
    list_productHerbPart_ID: [],
    list_testkit: [],
    list_testkit_ID: [],
    list_relatedType: [],
    list_pdTypeIcon: [
      "fa-pills",
      "fa-capsules",
      "fa-seedling",
      "fa-tablets",
      "fa-prescription-bottle",
      "fa-stethoscope",
      "fa-magic",
      "fa-utensils",
      "fa-vial",
      "fa-skull"
    ],
    temp_fdaInfo: [],
    hasMfDetail: false,
    menu_mfd: false,
    menu_exd: false,
    menu_dateOfUse: false,
    menu_dateOfStop: false,
    maxdate: new Date().toISOString().substr(0, 10),
    show_modalBox_drugSet: false,
    show_modalBox_testkit: false,
    s2_temp_drugImg: [],
    s2_temp_drugName: "",
    s2_temp_drugAsset: "",
    s2_temp_drugInfo: "",
    s2_temp_tk_img: [],
    s2_temp_tk_name: "",
    s2_temp_tk_id: "",
    s2_temp_tk_isPositive: "0",
    s2_temp_knownDrug: false,
    s2_pd_photos: []
  }),
  computed: {
    productTypeErrors() {
      const errors = [];
      if (!this.$v.s2_pd_detail.pd_pdTypeName.$dirty) return errors;
      !this.$v.s2_pd_detail.pd_pdTypeName.required &&
        errors.push("กรุณาระบุประเภทของผลิตภัณฑ์");
      return errors;
    },
    productNameErrors() {
      const errors = [];
      if (
        this.s2_pd_detail.pd_nameTH.length < 1 &&
        this.s2_pd_detail.pd_cNameTH.length < 1 &&
        this.s2_pd_detail.pd_cNameEN.length < 1 &&
        this.s2_pd_detail.pd_fNameTH.length < 1 &&
        this.s2_pd_detail.pd_fNameEN.length < 1
      ) {
        errors.push("กรุณาระบุชื่อผลิตภัณฑ์อย่างน้อย 1 ชื่อ");
      }
      return errors;
    },
    DateFormatted_MFD() {
      let originYear = this.s2_pd_manufacturer.pd_mf_mfd.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s2_pd_manufacturer.pd_mf_mfd.replace(
        originYear,
        cvYearBE
      );
      return this.s2_pd_manufacturer.pd_mf_mfd
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    },
    DateFormatted_EXD() {
      let originYear = this.s2_pd_manufacturer.pd_mf_exd.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s2_pd_manufacturer.pd_mf_exd.replace(
        originYear,
        cvYearBE
      );
      return this.s2_pd_manufacturer.pd_mf_exd
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    },
    DateFormatted_DOU() {
      let originYear = this.s2_pd_duration.pd_dur_dateOfUse.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s2_pd_duration.pd_dur_dateOfUse.replace(
        originYear,
        cvYearBE
      );
      return this.s2_pd_duration.pd_dur_dateOfUse
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    },
    DateFormatted_DOS() {
      let originYear = this.s2_pd_duration.pd_dur_dateOfStop.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s2_pd_duration.pd_dur_dateOfStop.replace(
        originYear,
        cvYearBE
      );
      return this.s2_pd_duration.pd_dur_dateOfStop
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    },
    checkProps_pdType() {
      return this.s2_pd_detail.pd_pdTypeName;
    },
    checkProps_pdTypeList() {
      return this.list_productType;
    }
  },
  methods: {
    async fetchProductType() {
      // console.log('... PD TYPE ...');

      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/fetchChoiceList.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            requestType: "productType"
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log('PD TYPE - DATA: ', dataRes)
          if (dataRes[0]["status"] == "success") {
            this.list_productType = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.ProductTypeName))
            );
            this.list_productDescription = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.Description))
            );
          }
        })
        .catch(err => {
          console.log("ERR.requestAccess.fetchList.productType: ", err);
        });
    },
    async fetchLicenseStatus() {
      await fetch(
        "http://www.tawainetwork.org/mobileBackend/fetch_picker_fetchList.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            queryList_order: "licenseStatus"
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes);
          if (dataRes[0]["status"] == "true") {
            this.list_licenseStatus = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.CHOICE_LABEL))
            );
          }
        })
        .catch(err => {
          console.log("ERR.reqFetch.licenseStatus: ", err);
        });
    },
    async fetchRelateType() {
      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/fetchChoiceList.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            requestType: "related"
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "success") {
            this.list_relatedType = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.RelatedName))
            );
          }
        })
        .catch(err => {
          console.log("ERR.requestAccess.fetchList.relatedType: ", err);
        });
    },
    async FDASearch() {
      this.show_modalBox_fdaSearch = true;
      this.fdaSearch_loading = true;
      await fetch(
        "http://www.tawainetwork.org/mobileBackend/fdaSearchAPI.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            number_src: this.s2_pd_licenseInfo.pdlc_number
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          this.fdaSearch_loading = false;
          // console.log('FDA - DATA: ', dataRes);
          if (dataRes[0]["status"] == "true") {
            this.temp_fdaInfo = dataRes;
            this.fdaSearch_result = true;
          } else {
            this.fdaSearch_result = false;
          }
          // if(dataRes[0]['status'] == "true"){
          //   this.list_licenseStatus = JSON.parse(JSON.stringify(dataRes[1].map(label => label.CHOICE_LABEL)));
          // }
        })
        .catch(err => {
          console.log("ERR.reqFetch.FDA.licenseAPI: ", err);
        });
    },
    async uploadIMG() {
      if (this.s2_pd_photos.length > 0) {
        this.imgNotfound = false;
        var formData = new FormData();
        // console.log('prepare to caseProperties ', this.caseProperties);
        formData.append("reqType", "product");
        formData.append("caseID", this.caseProperties.caseID);
        formData.append("userID", this.userProperties.userID);
        // formData.append('image', this.s2_pd_photos[0])
        for (let index = 0; index < this.s2_pd_photos.length; index++) {
          formData.append("image[]", this.s2_pd_photos[index]);
        }

        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/uploadImages.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json"
            },
            body: formData
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes, ' TTTT: ', dataRes[dataRes.length-1])
            let stillAlive = false;
            for (let index = 0; index < dataRes.length - 1; index++) {
              if (dataRes[index]["status"] != "success") {
                stillAlive = false;
                break;
              } else {
                stillAlive = true;
              }
            }
            if (stillAlive) {
              let splitImgPath = dataRes[dataRes.length - 1].split(",");
              splitImgPath.pop();
              this.pd_photo_show = splitImgPath;
              this.imgStatus = true;
            } else {
              this.imgStatus = false;
            }
          })
          .catch(err => {
            console.log("ERR.upload.img.product: ", err);
          });
      } else {
        this.imgNotfound = true;
      }
    },
    nextStep(n) {
      if (n === this.steps) {
        if (this.caseProperties.caseType == 1) {
          var packObj_pd_data = {
            s2_pd_detail: this.s2_pd_detail,
            s2_pd_label: this.s2_pd_label,
            s2_pd_licenseInfo: this.s2_pd_licenseInfo,
            s2_pd_manufacturer: this.s2_pd_manufacturer,
            s2_pd_usage: this.s2_pd_usage,
            s2_pd_check: this.s2_pd_check,
            s2_pd_checkInfo: this.s2_pd_checkInfo,
            s2_drugSet: this.s2_drugSet,
            s2_testkit: this.s2_testkit,
            s2_pd_duration: this.s2_pd_duration,
            s2_pd_related: this.s2_pd_related
          };
          // console.log("PACKING...PRODUCT TYPE 1: ", this.caseProperties.caseType);
          EventBus.$emit("s2_pd_detail_case1", packObj_pd_data);
          EventBus.$emit("s2_photos_case1", this.pd_photo_show);
          EventBus.$emit("backFrom_productInfo_case1", 3);
        } else if (this.caseProperties.caseType == 2) {
          var packObj_pd_data = {
            s2_pd_detail: this.s2_pd_detail,
            s2_pd_label: this.s2_pd_label,
            s2_pd_licenseInfo: this.s2_pd_licenseInfo,
            s2_pd_manufacturer: this.s2_pd_manufacturer,
            s2_pd_usage: this.s2_pd_usage,
            s2_pd_check: this.s2_pd_check,
            s2_pd_checkInfo: this.s2_pd_checkInfo,
            s2_drugSet: this.s2_drugSet,
            s2_testkit: this.s2_testkit
          };
          // console.log("PACKING...PRODUCT TYPE 2: ", this.caseProperties.caseType);
          EventBus.$emit("s2_pd_detail_case2", packObj_pd_data);
          EventBus.$emit("s2_photos_case2", this.pd_photo_show);
          EventBus.$emit("backFrom_productInfo_case2", 3);
        }
      } else if (n == 1) {
        this.$v.$touch();
        if (this.valid_step1 && this.s2_pd_detail.pd_pdTypeName.length > 0) {
          this.compStep = n + 1;
        }
      } else if (n == 2) {
        this.$v.$touch();
        if (
          this.valid_step2 &&
          (this.s2_pd_detail.pd_nameTH.length > 0 ||
            this.s2_pd_detail.pd_cNameTH.length > 0 ||
            this.s2_pd_detail.pd_cNameEN.length > 0 ||
            this.s2_pd_detail.pd_fNameTH.length > 0 ||
            this.s2_pd_detail.pd_fNameEN.length > 0)
        ) {
          this.compStep = n + 1;
        }
      } else {
        this.compStep = n + 1;
      }
    },
    backStep(n) {
      if (n == 1) {
        this.compStep = 1;
        if (this.caseProperties.caseType == 1) {
          EventBus.$emit("backFrom_productInfo_case1", this.compStep);
        } else if (this.caseProperties.caseType == 2) {
          EventBus.$emit("backFrom_productInfo_case2", this.compStep);
        }
      } else {
        this.compStep = n - 1;
      }
    },
    // When change product type, all value of step below them must be reset ***
    async getPDTypeINDEX() {
      // console.log('CheckType_Sync ',this.s2_pd_detail.pd_pdTypeName);

      if (
        this.s2_pd_detail.pd_pdTypeName.length > 0 &&
        this.list_productType.length > 0
      ) {
        let pdTypeINDEX = this.list_productType.findIndex(
          index => index === this.s2_pd_detail.pd_pdTypeName
        );
        this.s2_pd_detail.pd_pdTypeIndex = pdTypeINDEX += 1;

        // console.log('DO IT: ', pdTypeINDEX, this.list_productType);

        if (pdTypeINDEX != 6 && pdTypeINDEX != 10) {
          // GET PRODUCT_KING Properties request type
          let requestType_PK = "";
          let requestType_INST = "";
          let requestSubType = "";
          let requestHerbPart = "";
          if (pdTypeINDEX == 1 || pdTypeINDEX == 2 || pdTypeINDEX == 4) {
            requestType_PK = "productKind";
            requestType_INST = "productInst_all";
            requestSubType = "1";
          } else if (pdTypeINDEX == 3) {
            requestType_PK = "productKind_herb";
            requestType_INST = "productInst_all";
            requestHerbPart = "product_herbPart";
            requestSubType = "empty";
          } else if (pdTypeINDEX == 5) {
            requestType_PK = "productKind_MedGroup";
            requestType_INST = "productInst_05";
            requestSubType = "empty";
          } else if (pdTypeINDEX == 7) {
            requestType_PK = "productKind";
            requestType_INST = "productInst_07";
            requestSubType = "2";
          } else if (pdTypeINDEX == 8 || pdTypeINDEX == 9) {
            requestType_PK = "productKind";
            requestType_INST = "productInst_0809";
            requestSubType = "3";
          }

          // GET TESTKIT BASED ON PRODUCT as Properties request type
          let requestType_TK = "";
          let requestSubType_TK = "";
          if (pdTypeINDEX == 2) {
            requestType_TK = "testkit_byID";
            requestSubType_TK = "1";
          } else if (pdTypeINDEX == 5) {
            requestType_TK = "testkit_polyMed";
            requestSubType_TK = "";
          } else if (pdTypeINDEX == 7) {
            requestType_TK = "testkit_byID";
            requestSubType_TK = "2";
          } else if (pdTypeINDEX == 8 || pdTypeINDEX == 9) {
            requestType_TK = "testkit_byID";
            requestSubType_TK = "3";
          }

          // fetch kind of product
          await fetch(
            "http://www.tawainetwork.org/webBackendPHP/fetchChoiceList.php",
            {
              method: "POST",
              headers: {
                Accept: "application/json",
                "Content-Type": "application/json"
              },
              body: JSON.stringify({
                requestType: requestType_PK,
                subRequest: requestSubType
              })
            }
          )
            .then(res => {
              return res.json();
            })
            .then(dataRes => {
              // console.log('PK - DATA: ', dataRes)
              if (dataRes[0]["status"] == "success") {
                this.list_productKind = JSON.parse(
                  JSON.stringify(dataRes[1].map(label => label.ProductKindName))
                );
                this.list_productKind_ID = JSON.parse(
                  JSON.stringify(dataRes[1].map(label => label.ProductKindId))
                );
              }
            })
            .catch(err => {
              console.log("ERR.requestAccess.fetchList.productKind: ", err);
            });

          // fetch instruction of product
          await fetch(
            "http://www.tawainetwork.org/webBackendPHP/fetchChoiceList.php",
            {
              method: "POST",
              headers: {
                Accept: "application/json",
                "Content-Type": "application/json"
              },
              body: JSON.stringify({
                requestType: requestType_INST
              })
            }
          )
            .then(res => {
              return res.json();
            })
            .then(dataRes => {
              // console.log('INST - DATA: ', dataRes)
              if (dataRes[0]["status"] == "success") {
                this.list_productInstruction = JSON.parse(
                  JSON.stringify(dataRes[1].map(label => label.InstructionName))
                );
                this.list_productInstruction_ID = JSON.parse(
                  JSON.stringify(dataRes[1].map(label => label.InstructionId))
                );
              }
            })
            .catch(err => {
              console.log("ERR.requestAccess.fetchList.productKind: ", err);
            });

          if (pdTypeINDEX == 3) {
            await fetch(
              "http://www.tawainetwork.org/webBackendPHP/fetchChoiceList.php",
              {
                method: "POST",
                headers: {
                  Accept: "application/json",
                  "Content-Type": "application/json"
                },
                body: JSON.stringify({
                  requestType: requestHerbPart
                })
              }
            )
              .then(res => {
                return res.json();
              })
              .then(dataRes => {
                // console.log('PK - DATA: ', dataRes)
                if (dataRes[0]["status"] == "success") {
                  this.list_productHerbPart = JSON.parse(
                    JSON.stringify(
                      dataRes[1].map(label => label.HerbalPartName)
                    )
                  );
                  this.list_productHerbPart_ID = JSON.parse(
                    JSON.stringify(dataRes[1].map(label => label.HerbalPartId))
                  );
                }
              })
              .catch(err => {
                console.log(
                  "ERR.requestAccess.fetchList.productHerbPart: ",
                  err
                );
              });
          }

          if (
            pdTypeINDEX == 2 ||
            pdTypeINDEX == 5 ||
            pdTypeINDEX == 7 ||
            pdTypeINDEX == 8 ||
            pdTypeINDEX == 9
          ) {
            await fetch(
              "http://www.tawainetwork.org/webBackendPHP/fetchChoiceList.php",
              {
                method: "POST",
                headers: {
                  Accept: "application/json",
                  "Content-Type": "application/json"
                },
                body: JSON.stringify({
                  requestType: requestType_TK,
                  subRequest: requestSubType_TK
                })
              }
            )
              .then(res => {
                return res.json();
              })
              .then(dataRes => {
                // console.log('TK_LIST - DATA: ', dataRes)
                if (dataRes[0]["status"] == "success") {
                  this.list_testkit = JSON.parse(
                    JSON.stringify(dataRes[1].map(label => label.TestkitName))
                  );
                  this.list_testkit_ID = JSON.parse(
                    JSON.stringify(dataRes[1].map(label => label.TestkitId))
                  );
                }
              })
              .catch(err => {
                console.log("ERR.requestAccess.fetchList.testkit: ", err);
              });
          }
        }
      }
    },
    checkIsHasMFdetail() {
      if (this.s2_pd_manufacturer.pd_mf_owner.length > 0) {
        this.hasMfDetail = true;
      }
    },
    getPKTypeINDEX() {
      let PK_TYPE_INDEX = this.list_productKind.findIndex(
        index => index === this.s2_pd_usage.pd_us_KindName
      );
      // console.log("PK_INDEX# ", this.list_productKind_ID[PK_TYPE_INDEX]);
      this.s2_pd_usage.pd_us_KindIndex = this.list_productKind_ID[
        PK_TYPE_INDEX
      ];
    },
    getPInstTypeINDEX() {
      let PInst_TYPE_INDEX = this.list_productInstruction.findIndex(
        index => index === this.s2_pd_usage.pd_us_InstName
      );
      // console.log("PInst_INDEX# ", this.list_productInstruction_ID[PInst_TYPE_INDEX]);
      this.s2_pd_usage.pd_us_InstIndex = this.list_productInstruction_ID[
        PInst_TYPE_INDEX
      ];
    },
    getHerbPartINDEX() {
      let HP_TYPE_INDEX = this.list_productHerbPart.findIndex(
        index => index === this.s2_pd_detail.pd_hpTypeName
      );
      // console.log("PInst_INDEX# ", this.list_productHerbPart_ID[HP_TYPE_INDEX]);
      this.s2_pd_detail.pd_hpTypeIndex = this.list_productHerbPart_ID[
        HP_TYPE_INDEX
      ];
    },
    getTestkitINDEX() {
      let TK_TYPE_INDEX = this.list_testkit.findIndex(
        index => index === this.s2_temp_tk_name
      );
      this.s2_temp_tk_id = this.list_testkit_ID[TK_TYPE_INDEX];
      // console.log("TK_TYPE_INDEX# ", this.list_testkit_ID[TK_TYPE_INDEX]);
    },
    getRelatedINDEX() {
      let related_TYPE_INDEX = this.list_relatedType.findIndex(
        index => index === this.s2_pd_related.pd_check_relatedName
      );
      this.s2_pd_related.pd_check_relatedID = related_TYPE_INDEX + 1;
    },
    fn_match_FDAinfo(index) {
      // console.log('SELECT FDA CARD INDEX: ', index);

      this.show_modalBox_fdaSearch = false;
      this.$set(
        this.s2_pd_licenseInfo,
        "pdlc_number",
        this.temp_fdaInfo[index].FDA_license
      );
      this.$set(
        this.s2_pd_licenseInfo,
        "pdlc_status",
        this.temp_fdaInfo[index].FDA_status
      );
      this.$set(this.s2_pd_licenseInfo, "pdlc_isMatch", "1");

      this.hasMfDetail = true;

      this.$set(
        this.s2_pd_manufacturer,
        "pd_mf_owner",
        this.temp_fdaInfo[index].FDA_owner
      );
      this.$set(
        this.s2_pd_manufacturer,
        "pd_mf_name",
        this.temp_fdaInfo[index].FDA_manufact
      );
      this.$set(
        this.s2_pd_manufacturer,
        "pd_mf_address",
        this.temp_fdaInfo[index].FDA_manufactLocation
      );
      this.$set(
        this.s2_pd_manufacturer,
        "pd_mf_newcode",
        this.temp_fdaInfo[index].FDA_newcode
      );

      this.$set(
        this.s2_pd_detail,
        "pd_fNameTH",
        this.temp_fdaInfo[index].FDA_nameTH
      );
      this.$set(
        this.s2_pd_detail,
        "pd_fNameEN",
        this.temp_fdaInfo[index].FDA_nameENG
      );
    },
    fn_s2_checkHasLabel() {
      if (!this.s2_pd_label.pd_hasLabel) {
        this.s2_pd_label = {
          pd_hasLabel: false,
          pd_hasLicense: false
        };
        this.s2_pd_licenseInfo = {
          pdlc_number: "",
          pdlc_status: "",
          pdlc_isMatch: "0"
        };
      }
    },
    fn_s2_checkHasLicense() {
      if (!this.s2_pd_label.pd_hasLicense) {
        this.s2_pd_licenseInfo = {
          pdlc_number: "",
          pdlc_status: "",
          pdlc_isMatch: "0"
        };
      }
    },
    fn_s2_checkHasManufacturer() {
      if (!this.hasMfDetail) {
        this.s2_pd_manufacturer = {
          pd_mf_owner: "",
          pd_mf_name: "",
          pd_mf_address: "",
          pd_mf_lot: "",
          pd_mf_mfd: "",
          pd_mf_exd: "",
          pd_mf_newcode: ""
        };
      }
    },
    fn_s2_checkContUsing() {
      if (this.s2_pd_duration.pd_dur_contUse) {
        this.$set(this.s2_pd_duration, "pd_dur_dateOfStop", "");
      }
    },
    addDrugSet() {
      if (this.s2_pd_detail.pd_pdTypeIndex == 3) {
        if (
          this.s2_temp_drugImg.name.length > 0 &&
          this.s2_temp_drugName.length > 0
        ) {
          this.s2_drugSet.push({
            url: this.s2_temp_drugImg,
            imgName: this.s2_temp_drugImg.name,
            name:
              this.s2_temp_drugName +
              ", " +
              this.s2_temp_drugAsset +
              " " +
              this.s2_temp_drugInfo
          });
          this.s2_temp_drugImg = [];
          this.s2_temp_drugName = "";
          this.s2_temp_drugAsset = "";
          this.s2_temp_drugInfo = "";
          this.show_modalBox_drugSet = false;
        }
        // console.log(this.s2_drugSet);
      } else {
        if (this.s2_temp_drugImg.name.length > 0) {
          this.s2_drugSet.push({
            url: this.s2_temp_drugImg,
            imgName: this.s2_temp_drugImg.name,
            name: this.s2_temp_drugName
          });
          this.s2_temp_drugImg = [];
          this.s2_temp_drugName = "";
          (this.s2_temp_knownDrug = false),
            (this.show_modalBox_drugSet = false);
        }
      }
    },
    delDrugSet(index) {
      this.s2_drugSet.splice(index, 1);
    },
    addTestkit() {
      let tempPositiveName = "";
      if (
        this.s2_temp_tk_img.name.length > 0 &&
        this.s2_temp_tk_name.length > 0
      ) {
        if (parseInt(this.s2_temp_tk_isPositive)) {
          tempPositiveName = "พบสารปนเปื้อน";
        } else {
          tempPositiveName = "ไม่พบสารปนเปื้อน";
        }
        this.s2_testkit.push({
          url: this.s2_temp_tk_img,
          imgName: this.s2_temp_tk_img.name,
          name: this.s2_temp_tk_name,
          testkit_id: this.s2_temp_tk_id,
          isPositive: this.s2_temp_tk_isPositive,
          isPositiveName: tempPositiveName
        });
        this.s2_temp_tk_img = [];
        this.s2_temp_tk_name = "";
        this.s2_temp_tk_id = "";
        this.s2_temp_tk_isPositive = "0";
        this.show_modalBox_testkit = false;
      }
    },
    delTestkit(index) {
      this.s2_testkit.splice(index, 1);
    }
  },
  created() {
    this.userProperties = this.$store.getters.getUserProperties;
    this.caseProperties = this.$store.getters.getCaseProperties;
  },
  mounted() {
    this.fetchProductType();
    this.fetchLicenseStatus();
    if (this.userProperties.userRole) {
      this.fetchRelateType();
    }
    // console.log("Product-INFO: ", this.caseProperties);
  }
};
</script>

