<template>
  <div class="bodyContent">
    <!-- Edit mode CASE -->
    <v-overlay :value="loadingQuery">
      <v-progress-circular
        indeterminate
        size="64"
        :width="7"
      ></v-progress-circular>
    </v-overlay>

    <!-- Missing Data when fill the form -->
    <v-dialog v-model="dataMissingModal" persistent max-width="350">
      <v-card class="pt-8" align="center">
        <v-icon align="center" color="red darken-3" size="80">
          fa-exclamation-triangle
        </v-icon>
        <p class="text_title_alert">ขออภัย พบข้อผิดพลาด</p>
        <v-card-text align="left"
          >ข้อมูลผู้ใช้ขาดหายระหว่างดำเนินการกรอกข้อมูล
          กลับสู่หน้าผู้ใช้งานหลักและเลือกแก้ไขรายงาน</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn rounded large text color="red darken-1" @click="exitReport"
            >รับทราบ</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Confirm Case (final step to sending the case, user cann't change or edit later) -->
    <v-dialog v-model="caseSendingModal" persistent max-width="350">
      <v-card align="center" :loading="loadingCaseSending">
        <v-icon
          class="mt-8"
          align="center"
          color="light-blue lighten-2"
          size="80"
        >
          fa-file-alt
        </v-icon>
        <p class="text_title_question">ยืนยันการส่งรายงาน</p>
        <v-card-text align="left"
          >หากท่านยืนยันการส่งรายงาน รายงานฉบับดังกล่าวจะไม่สามารถแก้ไขได้อีก
          และระบบจะทำการส่งมอบให้เจ้าที่รับผิดชอบ</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            rounded
            large
            text
            color="grey lighten-1"
            @click="caseSendingModal = false"
            :disabled="loadingCaseSending"
            >ยกเลิก</v-btn
          >
          <v-btn
            rounded
            large
            text
            color="light-blue darken-1"
            @click="sendingCase"
            :disabled="loadingCaseSending"
            >ยืนยัน</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Result of sending case report to server: Success condition -->
    <v-dialog v-model="caseResultModal" persistent max-width="350">
      <v-card align="center">
        <v-icon class="mt-8" align="center" color="green lighten-2" size="80">
          fa-check
        </v-icon>
        <p class="text_title_success">ส่งรายงานสำเร็จ</p>
        <v-card-text align="left"
          >รายงานดังกล่าวถูกส่งมอบให้เจ้าที่รับผิดชอบแล้ว
          ท่านสามารถติดตามสถานะการดำเนินงานได้ที่หน้าผู้ใช้งานหลัก</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            rounded
            large
            text
            color="green darken-1"
            @click="confirmResult"
            >ยืนยัน</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- SnackBar handler update case to server status -->
    <v-snackbar v-model="snb_updateStatus" :timeout="snb_timeout">
      {{ message_UpdateStatus }}
      <v-btn :color="snb_color" text @click="snb_updateStatus = false">
        ตกลง
      </v-btn>
    </v-snackbar>

    <!-- Header of Content -->
    <v-row>
      <v-col cols="12">
        <div class="pt-6 ml-6">
          <p class="header_1">รายงานผลิตภัณฑ์ต้องสงสัย</p>
        </div>
      </v-col>
    </v-row>

    <!-- Content forms -->
    <v-row>
      <v-col cols="12" justify="center" align="center">
        <div class="pt-6">
          <v-stepper class="elevation-0" v-model="compStep">
            <template>
              <!-- Setup stepper detail -->
              <v-stepper-header class="elevation-0">
                <v-stepper-step :complete="compStep > 1" step="1">{{
                  stepName[0]
                }}</v-stepper-step>
                <v-divider></v-divider>
                <v-stepper-step :complete="compStep > 2" step="2">
                  {{ stepName[1] }}
                  <small class="mt-2">มี 4 ขั้นตอน</small>
                </v-stepper-step>
                <v-divider></v-divider>
                <v-stepper-step :complete="compStep > 3" step="3">{{
                  stepName[2]
                }}</v-stepper-step>
                <v-divider></v-divider>
                <v-stepper-step step="4">{{ stepName[3] }}</v-stepper-step>
              </v-stepper-header>

              <!-- Item in stepper -->
              <v-stepper-items>
                <v-stepper-content
                  class="pa-5"
                  v-for="n in steps"
                  :key="`${n}-content`"
                  :step="n"
                >
                  <!-- Step 1: userInfo and Product discovery -->
                  <div v-if="n == 1">
                    <v-row justify="start">
                      <v-col cols="12" md="6" align="left">
                        <p class="header_form">
                          ข้อมูลการพบเจอผู้ใช้ผลิตภัณฑ์ต้องสงสัย
                        </p>
                      </v-col>
                    </v-row>

                    <!-- hasUser -->
                    <v-form ref="form" v-model="valid_userInfo" lazy-validation>
                      <v-row justify="start">
                        <v-col cols="12" class="py-0">
                          <v-checkbox
                            class="py-0"
                            v-model="s1_user_info.us_hasUser"
                            label="ท่านพบผู้ใช้ผลิตภัณฑ์ต้องสงสัยหรือไม่"
                            @change="fn_s1_checkHasUser"
                          >
                          </v-checkbox>
                        </v-col>

                        <v-row align="center" class="py-4 px-6">
                          <v-col cols="12" md="6" align="left" class="py-0">
                            <v-text-field
                              label="ชื่อ"
                              outlined
                              rounded
                              v-model="s1_user_info.us_fName"
                              :disabled="!s1_user_info.us_hasUser"
                              :filled="!s1_user_info.us_hasUser"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="12" md="6" align="left" class="py-0">
                            <v-text-field
                              label="นามสกุล"
                              outlined
                              rounded
                              v-model="s1_user_info.us_lName"
                              :disabled="!s1_user_info.us_hasUser"
                              :filled="!s1_user_info.us_hasUser"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="6" md="6" align="left" class="py-0">
                            <v-select
                              label="เลือกเพศ *"
                              outlined
                              rounded
                              v-model="s1_user_info.us_gender"
                              :items="list_gender"
                              :disabled="!s1_user_info.us_hasUser"
                              :filled="!s1_user_info.us_hasUser"
                              :error-messages="userInfoErrors_gender"
                              required
                              @input="$v.s1_user_info.us_gender.$touch()"
                              @blur="$v.s1_user_info.us_gender.$touch()"
                            ></v-select>
                          </v-col>
                          <v-col cols="6" md="6" align="left" class="py-0">
                            <v-text-field
                              label="อายุ (ปี) *"
                              outlined
                              rounded
                              v-model="s1_user_info.us_age"
                              type="number"
                              :disabled="!s1_user_info.us_hasUser"
                              :filled="!s1_user_info.us_hasUser"
                              :error-messages="userInfoErrors_age"
                              required
                              @input="$v.s1_user_info.us_age.$touch()"
                              @blur="$v.s1_user_info.us_age.$touch()"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="6" md="6" align="left" class="py-0">
                            <v-text-field
                              label="น้ำหนัก (กิโลกรัม) *"
                              outlined
                              rounded
                              v-model="s1_user_info.us_weight"
                              type="number"
                              :disabled="!s1_user_info.us_hasUser"
                              :filled="!s1_user_info.us_hasUser"
                              :error-messages="userInfoErrors_weight"
                              required
                              @input="$v.s1_user_info.us_weight.$touch()"
                              @blur="$v.s1_user_info.us_weight.$touch()"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="6" md="6" align="left" class="py-0">
                            <v-text-field
                              label="เบอร์โทรศัพท์ติดต่อ"
                              outlined
                              rounded
                              v-mask="mask"
                              v-model="s1_user_info.us_contact"
                              :disabled="!s1_user_info.us_hasUser"
                              :filled="!s1_user_info.us_hasUser"
                            ></v-text-field>
                          </v-col>
                        </v-row>
                      </v-row>
                    </v-form>

                    <v-divider></v-divider>

                    <!-- discover location -->
                    <v-form ref="form" v-model="valid_discov" lazy-validation>
                      <v-row justify="start" class="mt-8">
                        <v-col cols="12" md="12" class="pt-0 px-6" align="left">
                          <p class="header_form">สถานที่พบผลิตภัณฑ์ต้องสงสัย</p>
                          <v-row class="pt-6">
                            <v-col cols="12" md="6" class="py-0">
                              <v-text-field
                                label="ที่อยู่"
                                outlined
                                rounded
                                v-model="s1_pd_discover.pdd_address"
                                :error-messages="AddressErrors"
                                required
                                @input="$v.s1_pd_discover.pdd_address.$touch()"
                                @blur="$v.s1_pd_discover.pdd_address.$touch()"
                              ></v-text-field>
                            </v-col>
                          </v-row>

                          <v-row>
                            <v-col cols="6" md="6" class="py-0">
                              <v-autocomplete
                                label=" เลือกจังหวัด"
                                item-text="arrProvince"
                                :items="arrProvince"
                                :change="selectProvince"
                                outlined
                                rounded
                                v-model="s1_pd_discover.pdd_province"
                                :error-messages="provErrors"
                                required
                                @input="$v.s1_pd_discover.pdd_province.$touch()"
                                @blur="$v.s1_pd_discover.pdd_province.$touch()"
                              ></v-autocomplete>
                            </v-col>

                            <v-col cols="6" md="6" class="py-0">
                              <v-autocomplete
                                label="เลือกอำเภอ/เขต"
                                item-text="arrDistrict"
                                :items="arrDistrict"
                                :change="selectDistrict"
                                outlined
                                rounded
                                v-model="s1_pd_discover.pdd_district"
                                :error-messages="districtErrors"
                                required
                                @input="$v.s1_pd_discover.pdd_district.$touch()"
                                @blur="$v.s1_pd_discover.pdd_district.$touch()"
                              ></v-autocomplete>
                            </v-col>

                            <v-col cols="6" md="6" class="py-0">
                              <v-autocomplete
                                label="เลือกตำบล/แขวง"
                                item-text="arrSubDistrict"
                                :items="arrSubDistrict"
                                outlined
                                rounded
                                v-model="s1_pd_discover.pdd_subDistrict"
                                :error-messages="subDistrictErrors"
                                required
                                @input="
                                  $v.s1_pd_discover.pdd_subDistrict.$touch()
                                "
                                @blur="
                                  $v.s1_pd_discover.pdd_subDistrict.$touch()
                                "
                              ></v-autocomplete>
                            </v-col>
                          </v-row>
                        </v-col>
                      </v-row>
                    </v-form>

                    <!-- Button onNextStep or Cancel -->
                    <v-row justify="center">
                      <v-btn
                        depressed
                        rounded
                        large
                        class="ma-8"
                        width="25%"
                        color="gray"
                        @click="exitReport()"
                        >ยกเลิก</v-btn
                      >
                      <v-btn
                        depressed
                        rounded
                        large
                        class="ma-8"
                        width="25%"
                        color="primary"
                        @click="nextStep(n)"
                        :disabled="!valid_discov"
                        >ถัดไป</v-btn
                      >
                    </v-row>
                  </div>

                  <!-- Step 2: Product information -->
                  <div v-else-if="n == 2">
                    <v-row justify="center" class="pa-0">
                      <ProductInfo
                        :caseProperties="caseProperties"
                        :s1_user_info="s1_user_info"
                        :s2_pd_detail="s2_pd_detail"
                        :s2_pd_label="s2_pd_label"
                        :s2_pd_licenseInfo="s2_pd_licenseInfo"
                        :s2_pd_manufacturer="s2_pd_manufacturer"
                        :s2_pd_usage="s2_pd_usage"
                        :s2_pd_check="s2_pd_check"
                        :s2_pd_checkInfo="s2_pd_checkInfo"
                        :s2_drugSet="s2_drugSet"
                        :s2_testkit="s2_testkit"
                        :pd_photo_show="s2_photos_show"
                      />
                    </v-row>
                  </div>

                  <!-- Step 3: Source of product information -->
                  <div v-else-if="n == 3">
                    <v-row justify="center" class="pa-4">
                      <ProductSource
                        :s3_src_detail="s3_src_detail"
                        :src_photo_show="s3_photos_show"
                      />
                    </v-row>
                  </div>

                  <!-- Step 4: Final report (summarise form *Can not edit*) -->
                  <div v-else-if="n == 4">
                    <ReportProduct
                      :s1_user_info="s1_user_info"
                      :s1_pd_discover="s1_pd_discover"
                      :s2_pd_detail="s2_pd_detail"
                      :s2_pd_label="s2_pd_label"
                      :s2_pd_licenseInfo="s2_pd_licenseInfo"
                      :s2_pd_manufacturer="s2_pd_manufacturer"
                      :s2_pd_usage="s2_pd_usage"
                      :s2_pd_check="s2_pd_check"
                      :s2_pd_checkInfo="s2_pd_checkInfo"
                      :s2_drugSet="s2_drugSet"
                      :s2_testkit="s2_testkit"
                      :s3_src_detail="s3_src_detail"
                      :s2_pd_photoShow="s2_photos_show"
                      :s3_src_photoShow="s3_photos_show"
                    />
                    <v-row justify="center">
                      <v-btn
                        depressed
                        rounded
                        large
                        class="ma-8"
                        width="25%"
                        color="gray"
                        @click="backStep(n)"
                        >ย้อนกลับ</v-btn
                      >
                      <v-btn
                        depressed
                        rounded
                        large
                        class="ma-8"
                        width="25%"
                        color="primary"
                        @click="nextStep(n)"
                        >ส่งรายงาน</v-btn
                      >
                    </v-row>
                  </div>
                </v-stepper-content>
              </v-stepper-items>
            </template>
          </v-stepper>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, maxLength } from "vuelidate/lib/validators";
import { mask } from "vue-the-mask";
// import { required } from 'vuelidate/lib/validators'
import EventBus from "./EventBus";
import ProductInfo from "./form/ProductInfo";
import ProductSource from "./form/ProductSource";
import ReportProduct from "./display/ReportProduct";

export default {
  mixins: [validationMixin],
  directives: { mask },
  validations: {
    s1_user_info: {
      us_gender: { required },
      us_age: { required, maxLength: maxLength(3) },
      us_weight: { required, maxLength: maxLength(3) }
    },
    s1_pd_discover: {
      pdd_address: { required },
      pdd_province: { required },
      pdd_district: { required },
      pdd_subDistrict: { required }
    }
  },
  components: {
    ProductInfo,
    ProductSource,
    ReportProduct
  },
  data: () => ({
    userProperties: {},
    caseProperties: {},
    dataMissingModal: false,
    caseSendingModal: false,
    caseResultModal: false,
    snb_updateStatus: false,
    message_UpdateStatus: "",
    snb_color: "",
    snb_timeout: 2000,
    compStep: 1,
    steps: 4,
    editMODE: true,
    loadingCaseSending: false,
    valid_userInfo: false,
    valid_discov: false,
    stepName: [
      "ข้อมูลการพบเจอผลิตภัณฑ์",
      "ข้อมูลผลิตภัณฑ์",
      "แหล่งที่มา",
      "สรุปรายงาน"
    ],
    arrProvinceID: [],
    arrDistrictID: [],
    arrSubDistrictID: [],
    arrProvince: [],
    arrDistrict: [],
    arrSubDistrict: [],
    mask: "###-###-####",
    list_gender: [],
    loadingQuery: false,
    // EXTRA VARIABLE
    extra_productID: "",
    // END EXTRA
    s1_user_info: {
      us_hasUser: false,
      us_fName: "",
      us_lName: "",
      us_gender: "",
      us_age: "",
      us_weight: "",
      us_contact: ""
    },
    s1_pd_discover: {
      pdd_address: "",
      pdd_province: "",
      pdd_district: "",
      pdd_subDistrict: ""
    },
    // S2_props_state...
    s2_pd_detail: {
      pd_pdTypeName: "",
      pd_pdTypeIndex: 1,
      pd_nameTH: "",
      pd_cNameTH: "",
      pd_cNameEN: "",
      pd_fNameTH: "",
      pd_fNameEN: "",
      pd_hpTypeName: "",
      pd_hpTypeIndex: 1,
      pd_hpInfo: ""
    },
    s2_pd_label: {
      pd_hasLabel: false,
      pd_hasLicense: false
    },
    s2_pd_licenseInfo: {
      pdlc_number: "",
      pdlc_status: "",
      pdlc_isMatch: "0"
    },
    s2_pd_manufacturer: {
      pd_mf_owner: "",
      pd_mf_name: "",
      pd_mf_address: "",
      pd_mf_lot: "",
      pd_mf_mfd: "",
      pd_mf_exd: "",
      pd_mf_newcode: ""
    },
    s2_pd_usage: {
      pd_us_KindName: "",
      pd_us_KindIndex: 0,
      pd_us_KindInfo: "",
      pd_us_InstName: "",
      pd_us_InstIndex: 0,
      pd_us_InstInfo: "",
      pd_us_volume: "",
      pd_us_price: "",
      pd_us_expected: "",
      pd_us_labelInfo: ""
    },
    s2_pd_check: {
      isCheckDMSC: false,
      isCheckQuality: false,
      isCheckAds: false,
      isCheckTestkit: false
    },
    s2_pd_checkInfo: {
      check_DMSC_status: "0",
      check_DMSC_info: "",
      check_quality_status: "0",
      check_quality_info: "",
      check_ads_status: "0",
      check_ads_info: ""
    },
    s2_drugSet: [],
    s2_testkit: [],
    // s3_props_state...
    s3_src_detail: {
      src_srcTypeName: "",
      src_srcTypeIndex: 0,
      src_subSrcTypeName: "",
      src_subSrcTypeIndex: 0,
      src_subInfoName: "",
      src_dateOnAir: "",
      src_timeOnAir: "",
      src_adrs: "",
      src_prov: "",
      src_dist: "",
      src_subDist: "",
      src_pName: "",
      src_pTel: "",
      src_pContact: "",
      src_pLicense: "",
      src_pCar: "",
      src_pCarColor: ""
    },
    s2_photos_show: [],
    s3_photos_show: [],
    drugSet_photos_show: [],
    testkit_photos_show: []
  }),
  computed: {
    selectProvince() {
      if (this.s1_pd_discover.pdd_province.length > 0) {
        this.fetchDistrict();
      }
      return 0;
    },
    selectDistrict() {
      if (this.s1_pd_discover.pdd_district.length > 0) {
        this.fetchSubDistrict();
      }
      return 0;
    },
    userInfoErrors_gender() {
      const errors = [];
      if (this.s1_user_info.us_hasUser) {
        !this.$v.s1_user_info.us_gender.required && errors.push("กรุณาระบุเพศ");
      }
      return errors;
    },
    userInfoErrors_age() {
      const errors = [];
      if (this.s1_user_info.us_hasUser) {
        !this.$v.s1_user_info.us_age.maxLength &&
          errors.push("กรุณาระบุข้อมูลให้ถูกต้อง");
        !this.$v.s1_user_info.us_age.required && errors.push("กรุณาระบุอายุ");
      }
      return errors;
    },
    userInfoErrors_weight() {
      const errors = [];
      if (this.s1_user_info.us_hasUser) {
        !this.$v.s1_user_info.us_weight.maxLength &&
          errors.push("กรุณาระบุข้อมูลให้ถูกต้อง");
        !this.$v.s1_user_info.us_weight.required &&
          errors.push("กรุณาระบุน้ำหนัก");
      }
      return errors;
    },
    AddressErrors() {
      const errors = [];
      if (!this.$v.s1_pd_discover.pdd_address.$dirty) return errors;
      !this.$v.s1_pd_discover.pdd_address.required &&
        errors.push("กรุณาระบุที่อยู่");
      return errors;
    },
    provErrors() {
      const errors = [];
      if (!this.$v.s1_pd_discover.pdd_province.$dirty) return errors;
      !this.$v.s1_pd_discover.pdd_province.required &&
        errors.push("กรุณาระบุจังหวัด");
      return errors;
    },
    districtErrors() {
      const errors = [];
      if (!this.$v.s1_pd_discover.pdd_district.$dirty) return errors;
      !this.$v.s1_pd_discover.pdd_district.required &&
        errors.push("กรุณาระบุอำเภอ/เขต");
      return errors;
    },
    subDistrictErrors() {
      const errors = [];
      if (!this.$v.s1_pd_discover.pdd_subDistrict.$dirty) return errors;
      !this.$v.s1_pd_discover.pdd_subDistrict.required &&
        errors.push("กรุณาระบุตำบล/แขวง");
      return errors;
    }
  },
  watch: {
    compStep() {
      // console.log("watcherWorking...");
      this.updateToServ();
    }
  },
  methods: {
    async queryProduct() {
      if (this.caseProperties.manageMode == "edit") {
        this.loadingQuery = true;
        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/getCaseDetailed.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              userID: this.userProperties.userID,
              caseID: this.caseProperties.caseID,
              caseTypeID: "2"
            })
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log('getDetailed.Product - DATA: ', dataRes)
            if (dataRes[0]["status"] == "success") {
              // Handler MFD
              let temp_MFD = "";

              if (dataRes[2]["ProduceDate"]) {
                temp_MFD = dataRes[2]["ProduceDate"]["date"];
                temp_MFD = temp_MFD.substr(0, 10);
              }
              // Handler EXD
              let temp_EXD = "";
              if (dataRes[2]["ExpireDate"]) {
                temp_EXD = dataRes[2]["ExpireDate"]["date"];
                temp_EXD = temp_EXD.substr(0, 10);
              }

              (this.s1_user_info = {
                us_hasUser: dataRes[1]["HaveAnyoneUse"],
                us_fName: dataRes[6]["FName"],
                us_lName: dataRes[6]["LName"],
                us_gender: dataRes[6]["Gender"],
                us_age: parseInt(dataRes[6]["Age"]),
                us_weight: parseInt(dataRes[6]["Weight"]),
                us_contact: dataRes[6]["TelNo"]
              }),
                (this.s1_pd_discover = {
                  pdd_address: dataRes[1]["FndAdr"],
                  pdd_province: dataRes[1]["FndProvince"],
                  pdd_district: dataRes[1]["FndDistrict"],
                  pdd_subDistrict: dataRes[1]["FndSubDistrict"]
                }),
                (this.s2_pd_detail = {
                  pd_pdTypeName: dataRes[2]["ProductTypeName"],
                  pd_pdTypeIndex: parseInt(dataRes[2]["ProductTypeId"]),
                  pd_nameTH: dataRes[2]["ProductName"],
                  pd_cNameTH: dataRes[2]["CommonNameTh"],
                  pd_cNameEN: dataRes[2]["CommonNameEn"],
                  pd_fNameTH: dataRes[2]["FormalNameTh"],
                  pd_fNameEN: dataRes[2]["FormalNameEn"],
                  pd_hpTypeName: dataRes[8]["PartName"],
                  pd_hpTypeIndex: parseInt(dataRes[8]["PartId"]),
                  pd_hpInfo: dataRes[8]["PartInfo"]
                });
              this.s2_pd_label = {
                pd_hasLabel: parseInt(dataRes[2]["IsHasThLabel"]),
                pd_hasLicense: parseInt(dataRes[2]["IsHasFDALicense"])
              };
              this.s2_pd_licenseInfo = {
                pdlc_number: dataRes[2]["FDACode"],
                pdlc_status: dataRes[2]["FDAStatus"],
                pdlc_isMatch: dataRes[2]["IsWrongLicense"].toString()
              };
              this.s2_pd_manufacturer = {
                pd_mf_owner: dataRes[2]["Producer"],
                pd_mf_name: dataRes[2]["ProducePlace"],
                pd_mf_address: dataRes[2]["Address"],
                pd_mf_lot: dataRes[2]["LotNo"],
                pd_mf_mfd: temp_MFD,
                pd_mf_exd: temp_EXD,
                pd_mf_newcode: dataRes[2]["NewCode"]
              };
              this.s2_pd_usage = {
                pd_us_KindName: dataRes[4]["ProductKindName"],
                pd_us_KindIndex: parseInt(dataRes[4]["ProductKindId"]),
                pd_us_KindInfo: dataRes[2]["ProductDetail"],
                pd_us_InstName: dataRes[4]["InstructionName"],
                pd_us_InstIndex: parseInt(dataRes[4]["InstructionId"]),
                pd_us_InstInfo: dataRes[2]["ProductKindIdDetail"],
                pd_us_volume: dataRes[2]["Dosage"],
                pd_us_price: parseInt(dataRes[2]["Price"]),
                pd_us_expected: dataRes[2]["Wishness"],
                pd_us_labelInfo: dataRes[2]["Properties"]
              };
              this.s2_pd_check = {
                isCheckDMSC: dataRes[2]["isCheckSciOrg"],
                isCheckQuality: dataRes[2]["isCheckLostQly"],
                isCheckAds: dataRes[2]["isCheckFakeAds"],
                isCheckTestkit: dataRes[2]["isCheckTestKit"]
              };
              this.s2_pd_checkInfo = {
                check_DMSC_status: dataRes[2]["IsInSciOrg"].toString(),
                check_DMSC_info: dataRes[2]["InSciOrgDetail"],
                check_quality_status: dataRes[2]["IsLostQly"].toString(),
                check_quality_info: dataRes[2]["LostQlyDetail"],
                check_ads_status: dataRes[2]["IsFakeAds"].toString(),
                check_ads_info: dataRes[2]["FakeAdsDetail"]
              };
              this.s2_drugSet = dataRes[5];
              this.s2_testkit = dataRes[7];

              // handler onAir Date
              let temp_extractDate = "";
              if (dataRes[3]["date"]) {
                temp_extractDate = dataRes[3]["date"]["date"];
                temp_extractDate = temp_extractDate.substr(0, 10);
              }

              var subSrcName = "";
              if (dataRes[3]["SubChannelName"]) {
                subSrcName = dataRes[3]["SubChannelName"];
              } else {
                subSrcName = dataRes[3]["subName"];
              }

              this.s3_src_detail = {
                src_srcTypeName: dataRes[3]["ChannelName"],
                src_srcTypeIndex: dataRes[3]["ChannelId"],
                src_subSrcTypeName: subSrcName,
                src_subSrcTypeIndex: dataRes[3]["ChannelSubId"],
                src_subInfoName: dataRes[3]["Name"],
                src_dateOnAir: temp_extractDate,
                src_timeOnAir: dataRes[3]["Period"],
                src_adrs: dataRes[3]["Adr"],
                src_prov: dataRes[3]["Province"],
                src_dist: dataRes[3]["District"],
                src_subDist: dataRes[3]["SubDistrict"],
                src_pName: dataRes[3]["details"],
                src_pTel: dataRes[3]["TelNo"],
                src_pContact: dataRes[3]["ContactMethod"],
                src_pLicense: dataRes[3]["LicenseNo"],
                src_pCar: dataRes[3]["CarBrand"],
                src_pCarColor: dataRes[3]["CarColor"]
              };

              if (dataRes[2]["photos"]) {
                let splitImgPath = dataRes[2]["photos"].split(",");
                if (splitImgPath.length > 1) {
                  splitImgPath.pop();
                }
                this.s2_photos_show = splitImgPath;
              } else {
                this.s2_photos_show = [];
              }
              if (dataRes[3]["srcPhotos"]) {
                let splitImgPath = dataRes[3]["srcPhotos"].split(",");
                if (splitImgPath.length > 1) {
                  splitImgPath.pop();
                }
                this.s3_photos_show = splitImgPath;
              } else {
                this.s3_photos_show = [];
              }
            }
          })
          .catch(err => {
            console.log("ERR.request.getCase.Product: ", err);
          });
        this.loadingQuery = false;
      }
    },
    async updateToServ() {
      // console.log("Update to serv...", this.s2_testkit);

      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/postUpdate_caseProduct.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            userID: this.userProperties.userID,
            caseID: this.caseProperties.caseID,
            editMODE: this.editMODE,
            s1_user_info: this.s1_user_info,
            s1_pd_discover: this.s1_pd_discover,
            s2_pd_detail: this.s2_pd_detail,
            s2_pd_label: this.s2_pd_label,
            s2_pd_licenseInfo: this.s2_pd_licenseInfo,
            s2_pd_manufacturer: this.s2_pd_manufacturer,
            s2_pd_usage: this.s2_pd_usage,
            s2_pd_check: this.s2_pd_check,
            s2_pd_checkInfo: this.s2_pd_checkInfo,
            s2_drugSet: this.s2_drugSet,
            s2_testkit: this.s2_testkit,
            s3_src_detail: this.s3_src_detail
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log('Up2Serv - DATA: ', dataRes);
          if (dataRes[0]["status"] == "failed") {
            this.snb_updateStatus = true;
            this.message_UpdateStatus =
              "เกิดข้อผิดพลาดในการอัปเดตข้อมูล กรุณาลองใหม่อีกครั้ง";
            this.snb_color = "pink";
          } else {
            if (this.loadingCaseSending && !this.editMODE) {
              this.loadingCaseSending = false;
              this.caseSendingModal = false;
              this.caseResultModal = true;
            } else {
              this.snb_updateStatus = true;
              this.message_UpdateStatus = "อัปเดตข้อมูลสำเร็จ";
              this.snb_color = "green";
              this.extra_productID = dataRes[1]["productID"];
            }
          }
        })
        .catch(err => {
          console.log("ERR.request.getCase.product: ", err);
        });
    },
    async uploadIMG_Type(imageStorage, requestType, outputType) {
      if (imageStorage.length > 0) {
        // console.log("TRY UPLOAD IMAGES: ", imageStorage);

        // this.imgNotfound = false;
        var formData = new FormData();
        // console.log('SomeoneCallMe...');
        formData.append("reqType", requestType);
        formData.append("caseID", this.caseProperties.caseID);
        formData.append("userID", this.userProperties.userID);
        formData.append("productID", this.extra_productID);
        // formData.append('image', imageStorage[0])
        for (let index = 0; index < imageStorage.length; index++) {
          // console.log("GET OVERLOOP IMAGE URL: ", imageStorage[index].url);
          formData.append("image[]", imageStorage[index].url);
        }

        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/uploadImages.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json"
            },
            body: formData
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(requestType, ' - DATA: ', dataRes);
            let stillAlive = false;
            for (let index = 0; index < dataRes.length - 1; index++) {
              if (dataRes[index]["status"] != "success") {
                stillAlive = false;
                break;
              } else {
                stillAlive = true;
              }
            }
            if (stillAlive) {
              let imageSet = dataRes[dataRes.length - 1];
              // console.log('IMG(TK or DS).RESPONSE IMAGE UPLOAD: ', imageSet);
              if (outputType == "drugSet") {
                for (let index = 0; index < imageSet.length; index++) {
                  this.s2_drugSet[index].url = imageSet[index];
                }
              } else if (outputType == "testkit") {
                for (let index = 0; index < imageSet.length; index++) {
                  // console.log("RESPONSE OVER INDEX: ", index, " IS ", imageSet[index]);
                  this.s2_testkit[index].url = imageSet[index];
                }
              }
            }
          })
          .catch(err => {
            console.log("ERR.upload.img.basedOnType: ", err);
          });
      }
    },
    async checkUserAuth_local() {
      let checkAuth = localStorage.getItem("userToken");
      if (checkAuth) {
        let userID = localStorage.getItem("userID");
        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/recheckSession.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              userID: userID,
              userToken: checkAuth
            })
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes)
            if (dataRes[0]["status"] == "failed") {
              this.$router.push("login");
              localStorage.removeItem("userToken");
              localStorage.removeItem("userID");
              localStorage.removeItem("userRole");
            } else {
              //wait for edit function and loadData from data to variable
              this.fetchGender();
              this.fetchProvince();
            }
          })
          .catch(err => {
            console.log(
              "ERR.requestAccess.Session_createReport.product: ",
              err
            );
          });
      } else {
        this.$router.push("/");
      }
    },
    async fetchGender() {
      await fetch(
        "http://www.tawainetwork.org/mobileBackend/fetch_picker_fetchList.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            queryList_order: "gender"
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes);
          if (dataRes[0]["status"] == "true") {
            this.list_gender = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.CHOICE_LABEL))
            );
          }
        })
        .catch(err => {
          console.log("ERR.reqFetch.gender: ", err);
        });
    },
    fetchProvince() {
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_province.php",
        {
          method: "GET",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          }
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrProvince = JSON.parse(
              JSON.stringify(dataRes[1].map(prov => prov.province))
            );
            this.arrProvinceID = JSON.parse(
              JSON.stringify(dataRes[1].map(provID => provID.province_code))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchProvince: ", err);
        });
    },
    fetchDistrict() {
      let provINDEX = this.arrProvince.findIndex(
        provINDEX => provINDEX === this.s1_pd_discover.pdd_province
      );
      // console.log('provID: ', this.arrProvinceID[provID])
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_amphoe.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            provinceCode: this.arrProvinceID[provINDEX]
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrDistrict = JSON.parse(
              JSON.stringify(dataRes[1].map(prov => prov.amphoe))
            );
            this.arrDistrictID = JSON.parse(
              JSON.stringify(dataRes[1].map(provID => provID.amphoe_code))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchAmphoe: ", err);
        });
    },
    fetchSubDistrict() {
      let districtINDEX = this.arrDistrict.findIndex(
        districtINDEX => districtINDEX === this.s1_pd_discover.pdd_district
      );
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_district.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            amphoeCode: this.arrDistrictID[districtINDEX]
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrSubDistrict = JSON.parse(
              JSON.stringify(dataRes[1].map(prov => prov.district))
            );
            this.arrSubDistrictID = JSON.parse(
              JSON.stringify(dataRes[1].map(provID => provID.district_code))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchSubDistrict: ", err);
        });
    },
    async getComp_s2() {
      await EventBus.$on("s2_pd_detail_case2", dataBUS => {
        // console.log("GET DATABUS.S2_PD_DETAIL: ", dataBUS);
        this.s2_pd_detail = dataBUS.s2_pd_detail;
        this.s2_pd_label = dataBUS.s2_pd_label;
        this.s2_pd_licenseInfo = dataBUS.s2_pd_licenseInfo;
        this.s2_pd_manufacturer = dataBUS.s2_pd_manufacturer;
        this.s2_pd_usage = dataBUS.s2_pd_usage;
        this.s2_pd_check = dataBUS.s2_pd_check;
        this.s2_pd_checkInfo = dataBUS.s2_pd_checkInfo;
        this.s2_drugSet = dataBUS.s2_drugSet;
        this.s2_testkit = dataBUS.s2_testkit;
        // this. = dataBUS;
      });
      await EventBus.$on("s2_photos_case2", dataBUS => {
        this.s2_photos_show = dataBUS;
      });
      await EventBus.$on("backFrom_productInfo_case2", dataBUS => {
        if (dataBUS == 3) {
          // console.log("PRODUCT_REPORT.SENDING UPLOAD_IMGS");
          this.uploadIMG_Type(this.s2_drugSet, "drugSet", "drugSet");
          this.uploadIMG_Type(this.s2_testkit, "testkit", "testkit");
          // console.log("GET TESTKit");
        }
        this.compStep = dataBUS;
      });
    },
    async getComp_s3() {
      await EventBus.$on("case_fakeAds_comp", dataBUS => {
        this.compStep = dataBUS;
      });
      await EventBus.$on("s3_src_detail", dataBUS => {
        // console.log('srcDetailed ', dataBUS);
        this.s3_src_detail = dataBUS;
      });
      await EventBus.$on("s3_photos", dataBUS => {
        this.s3_photos_show = dataBUS;
      });
    },
    nextStep(n) {
      if (n == this.steps) {
        this.caseSendingModal = true;
      } else if (n == 1) {
        this.$v.$touch();
        if (
          this.valid_discov &&
          this.s1_pd_discover.pdd_address.length > 0 &&
          this.s1_pd_discover.pdd_province.length > 0 &&
          this.s1_pd_discover.pdd_district.length > 0 &&
          this.s1_pd_discover.pdd_subDistrict.length > 0
        ) {
          if (!this.valid_userInfo) {
            if (
              this.s1_user_info.us_gender.length > 0 &&
              this.s1_user_info.us_age.length > 0 &&
              this.s1_user_info.us_weight.length > 0
            ) {
              this.compStep = n + 1;
            }
          } else {
            this.compStep = n + 1;
          }
        }
      } else {
        //
      }
    },
    backStep(n) {
      if (n === 1) {
        this.compStep = 1;
      } else {
        this.compStep = n - 1;
      }
    },
    exitReport() {
      this.dataMissingModal = false;
      this.$router.push("/mainuser");
    },
    sendingCase() {
      this.editMODE = false;
      this.loadingCaseSending = true;
      this.updateToServ();

      // Upload image of drugSet or Testkit
      // this.uploadIMG_Type(this.s2_drugSet, "drugSet", "drugSet");
      // this.uploadIMG_Type(this.s2_testkit, "testkit", "testkit");
    },
    confirmResult() {
      this.caseResultModal = false;
      this.$router.push("/mainuser");
      this.$destroy();
    },
    fn_s1_checkHasUser() {
      if (!this.s1_user_info.us_hasUser) {
        this.s1_user_info = {
          us_hasUser: false,
          us_fName: "",
          us_lName: "",
          us_age: "",
          us_gender: "",
          us_contact: ""
        };
      }
    }
  },
  created() {
    this.userProperties = this.$store.getters.getUserProperties;
    this.caseProperties = this.$store.getters.getCaseProperties;
    if (this.userProperties.userID.length < 1) {
      this.dataMissingModal = true;
    }
  },
  mounted() {
    this.checkUserAuth_local();
    this.getComp_s2();
    this.getComp_s3();
    this.queryProduct();
  }
};
</script>

