<template>
  <div class="bodyContent">
    <v-row justify="start">
      <v-col cols="12" align="left">
        <p class="header_form">ประเภทของแหล่งที่มา/จุดเผยแพร่โฆษณา</p>
      </v-col>
    </v-row>

    <v-row justify="start">
      <v-col cols="12" md="6" align="left">
        <v-select
          label="เลือกประเภทแหล่งที่มา"
          :items="list_sourceType"
          @change="getSRCTypeINDEX"
          outlined
          rounded
          v-model="s3_src_detail.src_srcTypeName"
        ></v-select>
      </v-col>
      <v-col cols="12" md="6" align="left">
        <div v-if="s3_src_detail.src_srcTypeName != ''" class="subtitle">
          <v-row>
            <v-col cols="1" class="pt-1">
              <v-avatar slot="icon" color="#00B5F8" size="20">
                <v-icon x-small color="white"> fa-info </v-icon>
              </v-avatar>
            </v-col>
            <v-col cols="11" class="px-1 py-1">
              <span class="subtitle_bold">
                {{ s3_src_detail.src_srcTypeName }}
              </span>
              <span class="subtitle">
                คือ
                {{
                  list_sourceDescription[s3_src_detail.src_srcTypeIndex - 1]
                }}</span
              >
            </v-col>
          </v-row>
        </div>
        <span v-else class="subtitle">
          กรุณาระบุประเภทของแหล่งที่มา/จุดเผยแพร่โฆษณา
        </span>
      </v-col>
    </v-row>

    <v-divider></v-divider>

    <v-row justify="start" class="mt-4">
      <v-col cols="12" align="left">
        <p class="header_form">รายละเอียดของแหล่งที่มา/จุดเผยแพร่โฆษณา</p>
      </v-col>
    </v-row>

    <!-- Properties detailed -->
    <v-row justify="start">
      <v-col cols="12" md="12" class="mx-3" align="left">
        <div v-if="this.s3_src_detail.src_srcTypeIndex - 1 == 0" class="mr-6">
          <p>รายละเอียดคลื่นวิทยุและวันเวลาที่ออกอากาศ</p>
          <v-row align="center">
            <v-col cols="6" md="6" align="left">
              <v-select
                label="เลือกประเภทคลื่นวิทยุ"
                :items="list_subType"
                @change="getSubSRCTypeINDEX"
                outlined
                rounded
                v-model="s3_src_detail.src_subSrcTypeName"
              ></v-select>
            </v-col>

            <v-col cols="6" md="6" align="left">
              <v-text-field
                label="คลื่นความถี่"
                outlined
                rounded
                v-model="s3_src_detail.src_subInfoName"
              ></v-text-field>
            </v-col>

            <v-col cols="6" md="6" align="left">
              <v-dialog
                ref="dialog"
                v-model="menu"
                :return-value.sync="s3_src_detail.src_dateOnAir"
                transition="scale-transition"
                persistent
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    :value="computedDateFormattedMomentjs"
                    outlined
                    rounded
                    label="วันที่ออกอากาศ"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="s3_src_detail.src_dateOnAir"
                  locale="th"
                  no-title
                  scrollable
                  :max="maxdate"
                >
                  <v-spacer></v-spacer>
                  <v-btn text color="primary" @click="menu = false"
                    >ยกเลิก</v-btn
                  >
                  <v-btn
                    text
                    color="primary"
                    @click="$refs.dialog.save(s3_src_detail.src_dateOnAir)"
                    >ยืนยัน</v-btn
                  >
                </v-date-picker>
              </v-dialog>
            </v-col>

            <v-col cols="6" md="6" align="left">
              <v-menu
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="s3_src_detail.src_timeOnAir"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="s3_src_detail.src_timeOnAir"
                    outlined
                    rounded
                    label="เวลาที่ออกอากาศ"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker
                  v-if="menu2"
                  v-model="s3_src_detail.src_timeOnAir"
                  full-width
                  format="24hr"
                  @click:minute="$refs.menu.save(s3_src_detail.src_timeOnAir)"
                ></v-time-picker>
              </v-menu>
            </v-col>
          </v-row>
        </div>

        <div v-if="this.s3_src_detail.src_srcTypeIndex - 1 == 1" class="mr-6">
          <p>รายละเอียดช่องโทรทัศน์และวันเวลาที่ออกอากาศ</p>
          <v-row>
            <v-col cols="6" md="6" class="py-0">
              <v-text-field
                label="ชื่อช่อง"
                outlined
                rounded
                v-model="s3_src_detail.src_subInfoName"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                label="ชื่อรายการ"
                outlined
                rounded
                v-model="s3_src_detail.src_subSrcTypeName"
              ></v-text-field>
            </v-col>
            <v-col cols="6" md="6" class="py-0">
              <v-dialog
                ref="dialog"
                v-model="menu"
                :return-value.sync="s3_src_detail.src_dateOnAir"
                transition="scale-transition"
                persistent
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    :value="computedDateFormattedMomentjs"
                    outlined
                    rounded
                    label="วันที่ออกอากาศ"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="s3_src_detail.src_dateOnAir"
                  locale="th"
                  no-title
                  scrollable
                  :max="maxdate"
                >
                  <v-spacer></v-spacer>
                  <v-btn
                    depressed
                    rounded
                    text
                    color="primary"
                    @click="menu = false"
                    >ยกเลิก</v-btn
                  >
                  <v-btn
                    depressed
                    rounded
                    text
                    color="primary"
                    @click="$refs.dialog.save(s3_src_detail.src_dateOnAir)"
                    >ยืนยัน</v-btn
                  >
                </v-date-picker>
              </v-dialog>
            </v-col>
            <v-col cols="6" md="6" class="py-0">
              <v-menu
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="s3_src_detail.src_timeOnAir"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="s3_src_detail.src_timeOnAir"
                    outlined
                    rounded
                    label="เวลาที่ออกอากาศ"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker
                  v-if="menu2"
                  v-model="s3_src_detail.src_timeOnAir"
                  full-width
                  format="24hr"
                  @click:minute="$refs.menu.save(s3_src_detail.src_timeOnAir)"
                ></v-time-picker>
              </v-menu>
            </v-col>
          </v-row>
        </div>

        <div
          v-if="
            this.s3_src_detail.src_srcTypeIndex - 1 == 2 ||
              this.s3_src_detail.src_srcTypeIndex - 1 == 3
          "
          class="mr-6"
        >
          <p v-if="this.s3_src_detail.src_srcTypeIndex - 1 == 2">
            รายละเอียดอินเทอร์เน็ต
          </p>
          <p v-else>รายละเอียดตลาดนัด</p>
          <v-row>
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                :label="
                  this.s3_src_detail.src_srcTypeIndex - 1 == 2
                    ? 'Link/URL'
                    : 'ชื่อตลาดนัด'
                "
                outlined
                rounded
                v-model="s3_src_detail.src_subInfoName"
              ></v-text-field>
            </v-col>
          </v-row>
        </div>

        <div
          v-if="
            this.s3_src_detail.src_srcTypeIndex - 1 == 4 ||
              this.s3_src_detail.src_srcTypeIndex - 1 == 5 ||
              this.s3_src_detail.src_srcTypeIndex - 1 == 6 ||
              this.s3_src_detail.src_srcTypeIndex - 1 == 7 ||
              this.s3_src_detail.src_srcTypeIndex - 1 == 8 ||
              this.s3_src_detail.src_srcTypeIndex - 1 == 9 ||
              this.s3_src_detail.src_srcTypeIndex - 1 == 11
          "
          class="mr-6"
        >
          <p>รายละเอียด{{ this.s3_src_detail.src_srcTypeName }}</p>
          <v-row>
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                :label="
                  this.s3_src_detail.src_srcTypeIndex - 1 == 9
                    ? 'ชื่อสถานที่ซื้อ/ได้รับ'
                    : 'ชื่อ' + this.s3_src_detail.src_srcTypeName
                "
                outlined
                rounded
                v-model="s3_src_detail.src_subInfoName"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6" class="py-0">
              <v-select
                :label="
                  this.s3_src_detail.src_srcTypeIndex - 1 == 6
                    ? 'วิธีการได้รับผลิตภัณฑ์'
                    : this.s3_src_detail.src_srcTypeIndex - 1 == 9
                    ? 'ความเกี่ยวข้องต่อบุคคลดังกล่าว'
                    : 'ประเภท' + this.s3_src_detail.src_srcTypeName
                "
                :items="list_subType"
                @change="getSubSRCTypeINDEX"
                outlined
                rounded
                v-model="s3_src_detail.src_subSrcTypeName"
              ></v-select>
            </v-col>
          </v-row>
        </div>

        <div v-if="this.s3_src_detail.src_srcTypeIndex - 1 == 10" class="mr-6">
          <p>รายละเอียดรถเร่/ขายตรง</p>
          <v-row>
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                label="ชื่อ-นามสกุลผู้ขาย"
                outlined
                rounded
                v-model="s3_src_detail.src_pName"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                label="เบอร์โทรติดต่อผู้ขาย"
                outlined
                rounded
                v-mask="mask"
                v-model="s3_src_detail.src_pTel"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                label="ช่องทางติดต่อผู้ขาย"
                outlined
                rounded
                v-model="s3_src_detail.src_pContact"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                label="ทะเบียนรถ"
                outlined
                rounded
                v-model="s3_src_detail.src_pLicense"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                label="ยี่ห้อรถ"
                outlined
                rounded
                v-model="s3_src_detail.src_pCar"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                label="สีรถ"
                outlined
                rounded
                v-model="s3_src_detail.src_pCarColor"
              ></v-text-field>
            </v-col>
          </v-row>
        </div>

        <div v-if="this.s3_src_detail.src_srcTypeIndex - 1 == 12" class="mr-6">
          <p>รายละเอียด</p>
          <v-row>
            <v-col cols="12" md="6" class="py-0">
              <v-textarea
                label="รายละเอียดแหล่งที่มา/จุดเผยแพร่โฆษณา"
                auto-grow
                outlined
                rounded
                v-model="s3_src_detail.src_subInfoName"
              ></v-textarea>
            </v-col>
          </v-row>
        </div>
      </v-col>
    </v-row>

    <!-- Location Information -->
    <v-row
      justify="center"
      v-if="
        this.s3_src_detail.src_srcTypeIndex - 1 == 0 ||
          this.s3_src_detail.src_srcTypeIndex - 1 >= 3
      "
    >
      <v-col cols="12" md="12" class="pt-0 px-6" align="left">
        <p>รายละเอียดตำแหน่ง/สถานที่พบจุดเผยแพร่</p>
        <v-row>
          <v-col cols="12" class="py-0">
            <v-text-field
              class="pt-2"
              label="ที่อยู่"
              outlined
              rounded
              v-model="s3_src_detail.src_adrs"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="12" md="6" class="py-0">
            <v-autocomplete
              label=" เลือกจังหวัด"
              item-text="arrProvince"
              :items="arrProvince"
              :change="selectProvince"
              outlined
              rounded
              v-model="s3_src_detail.src_prov"
            ></v-autocomplete>
          </v-col>

          <v-col cols="12" md="6" class="py-0">
            <v-autocomplete
              label="เลือกอำเภอ/เขต"
              item-text="arrDistrict"
              :items="arrDistrict"
              :change="selectDistrict"
              outlined
              rounded
              v-model="s3_src_detail.src_dist"
            ></v-autocomplete>
          </v-col>

          <v-col cols="12" md="6" class="py-0">
            <v-autocomplete
              label="เลือกตำบล/แขวง"
              item-text="arrSubDistrict"
              :items="arrSubDistrict"
              outlined
              rounded
              v-model="s3_src_detail.src_subDist"
            ></v-autocomplete>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <!-- Photo(s) -->
    <v-row justify="center">
      <v-col cols="12" class="pt-0 px-6" align="left">
        <div>
          <span class="mr-6">รูปภาพแหล่งที่มาหรือใช้ในการโฆษณา</span>
          <span v-if="!imgStatus" class="text_status_imgFailed"
            >(สถานะ ยังไม่บันทึก)</span
          >
          <span v-else class="text_status_imgSuccess"
            >(สถานะ บันทึกสำเร็จ)</span
          >
          <span class="text_warning_danger">
            ข้อควรระวัง: รูปภาพจะไม่ถูกบันทึก หากไม่กดปุ่มบันทึกรูปภาพ</span
          >
          <v-alert
            dismissible
            transition="scroll-x-transition"
            v-if="imgNotfound"
            type="error"
            class="mt-5"
          >
            กรุณาเลือกรูปภาพอย่างน้อยจำนวน 1 รูปภาพ
          </v-alert>
          <v-row>
            <v-col cols="12" md="6">
              <v-file-input
                outlined
                rounded
                v-model="s3_src_photo"
                show-size
                counter
                multiple
                prepend-inner-icon="$file"
                prepend-icon=""
                accept="image/*"
                label="คลิกเพื่อเพิ่มรูปภาพ"
              >
                <template v-slot:selection="{ text }">
                  <v-chip color="primary">
                    {{ text }}
                  </v-chip>
                </template>
              </v-file-input>
            </v-col>
            <v-col
              cols="12"
              md="6"
              class="mt-1"
              justify="center"
              align="center"
            >
              <v-btn
                depressed
                block
                rounded
                large
                color="primary"
                @click="uploadIMG"
                >บันทึกรูปภาพ</v-btn
              >
            </v-col>
          </v-row>
          <v-card outlined justify="center" align="center">
            <div
              v-if="src_photo_show.length == 0"
              justify="center"
              align="center"
              class="pa-5"
            >
              <v-icon class="py-5" size="130" color="#CDCDCD">fa-images</v-icon>
              <p>
                พื้นที่แสดงรูปภาพแหล่งที่มาหรือจุดเผยแพร่โฆษณา
                (รูปภาพจะปรากฎเมื่อทำการอัปโหลดสำเร็จ)
              </p>
              <span class="subtitle"
                >การอัปโหลดรูปภาพมายัง Tawai for Health</span
              >
              <br />
              <span class="subtitle"
                >ข้าพเจ้ายอมรับ ข้อตกลง Terms of Use and Privacy Policy ของทาง
                TaWai</span
              >
            </div>
            <div v-else class="pa-10">
              <v-row>
                <v-col
                  v-for="(n, index) in src_photo_show"
                  :key="index"
                  class="d-flex child-flex"
                  cols="3"
                >
                  <v-img
                    :src="n"
                    contain
                    aspect-ratio="1.3"
                    class="grey lighten-2"
                  >
                  </v-img>
                </v-col>
              </v-row>
            </div>
          </v-card>
        </div>
      </v-col>
    </v-row>

    <!-- Button previous or next -->
    <v-row justify="center">
      <v-btn
        depressed
        rounded
        large
        class="ma-8"
        width="25%"
        color="gray"
        @click="backStep()"
        >ย้อนกลับ</v-btn
      >
      <v-btn
        depressed
        rounded
        large
        class="ma-8"
        width="25%"
        color="primary"
        @click="nextStep()"
        >ถัดไป</v-btn
      >
    </v-row>
  </div>
</template>

<script>
import moment from "moment";
import { mask } from "vue-the-mask";
import EventBus from "../EventBus";
export default {
  props: ["s3_src_detail", "src_photo_show"],
  directives: {
    mask
  },
  data: () => ({
    userProperties: {},
    caseProperties: {},
    compStep: 3,
    list_sourceType: [],
    list_sourceDescription: [],
    list_subType: [],
    list_subTypeID: [],
    arrProvinceID: [],
    arrDistrictID: [],
    arrSubDistrictID: [],
    arrProvince: [],
    arrDistrict: [],
    arrSubDistrict: [],
    maxdate: new Date().toISOString().substr(0, 10),
    menu: false,
    menu2: false,
    mask: "##-####-####",
    imgNotfound: false,
    imgStatus: false,
    s3_src_photo: []
  }),
  computed: {
    computedDateFormattedMomentjs() {
      // console.log('YEAR: ', this.s3_src_detail.src_dateOnAir)
      let originYear = this.s3_src_detail.src_dateOnAir.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s3_src_detail.src_dateOnAir.replace(
        originYear,
        cvYearBE
      );
      // console.log('YEAR BE: ', originYear, ' TO: ', cvYearBE, ' AS: ', displayDate)
      return this.s3_src_detail.src_dateOnAir
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    },
    selectProvince() {
      if (this.s3_src_detail.src_prov.length > 0) {
        this.fetchDistrict();
      }
      return 0;
    },
    selectDistrict() {
      if (this.s3_src_detail.src_dist.length > 0) {
        this.fetchSubDistrict();
      }
      return 0;
    }
  },
  methods: {
    async fetchSourceType() {
      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/fetchChoiceList.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            requestType: "sourceType"
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "success") {
            this.list_sourceType = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.ChannelName))
            );
            this.list_sourceDescription = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.Description))
            );
          }
        })
        .catch(err => {
          console.log("ERR.requestAccess.fetchList.sourceType: ", err);
        });
    },
    async getSRCTypeINDEX() {
      let srcTypeINDEX = this.list_sourceType.findIndex(
        index => index === this.s3_src_detail.src_srcTypeName
      );
      let tempIndex = (srcTypeINDEX += 1);
      this.s3_src_detail.src_srcTypeIndex = tempIndex;
      if (
        tempIndex == 1 ||
        tempIndex == 5 ||
        tempIndex == 6 ||
        tempIndex == 7 ||
        tempIndex == 8 ||
        tempIndex == 9 ||
        tempIndex == 10 ||
        tempIndex == 12
      ) {
        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/fetchChoiceList.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              requestType: "subSoruceType",
              subRequest: tempIndex.toString()
            })
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes)
            if (dataRes[0]["status"] == "success") {
              this.list_subType = JSON.parse(
                JSON.stringify(dataRes[1].map(label => label.SubChannelName))
              );
              this.list_subTypeID = JSON.parse(
                JSON.stringify(dataRes[1].map(label => label.SubChannelId))
              );
            }
          })
          .catch(err => {
            console.log("ERR.requestAccess.fetchList.sourceType: ", err);
          });
      }
    },
    getSubSRCTypeINDEX() {
      let subSrcTypeINDEX = this.list_subType.findIndex(
        index => index === this.s3_src_detail.src_subSrcTypeName
      );
      // let tempIndex = subSrcTypeINDEX+=1
      // console.log("subCHType# ", this.list_subTypeID[subSrcTypeINDEX]);
      this.s3_src_detail.src_subSrcTypeIndex = parseInt(
        this.list_subTypeID[subSrcTypeINDEX]
      );
    },
    fetchMountProvince() {
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_province.php",
        {
          method: "GET",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          }
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrProvince = JSON.parse(
              JSON.stringify(dataRes[1].map(prov => prov.province))
            );
            this.arrProvinceID = JSON.parse(
              JSON.stringify(dataRes[1].map(provID => provID.province_code))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchMountProvince: ", err);
        });
    },
    fetchDistrict() {
      let provINDEX = this.arrProvince.findIndex(
        provINDEX => provINDEX === this.s3_src_detail.src_prov
      );
      // console.log('provID: ', this.arrProvinceID[provID])
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_amphoe.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            provinceCode: this.arrProvinceID[provINDEX]
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrDistrict = JSON.parse(
              JSON.stringify(dataRes[1].map(prov => prov.amphoe))
            );
            this.arrDistrictID = JSON.parse(
              JSON.stringify(dataRes[1].map(provID => provID.amphoe_code))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchAmphoe: ", err);
        });
    },
    fetchSubDistrict() {
      let districtINDEX = this.arrDistrict.findIndex(
        districtINDEX => districtINDEX === this.s3_src_detail.src_dist
      );
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_district.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            amphoeCode: this.arrDistrictID[districtINDEX]
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrSubDistrict = JSON.parse(
              JSON.stringify(dataRes[1].map(prov => prov.district))
            );
            this.arrSubDistrictID = JSON.parse(
              JSON.stringify(dataRes[1].map(provID => provID.district_code))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchSubDistrict: ", err);
        });
    },
    nextStep() {
      this.compStep = 4;
      EventBus.$emit("case_fakeAds_comp", this.compStep);
      EventBus.$emit("s3_src_detail", this.s3_src_detail);
      EventBus.$emit("s3_photos", this.src_photo_show);
    },
    backStep() {
      this.compStep = 2;
      EventBus.$emit("case_fakeAds_comp", this.compStep);
    },
    async uploadIMG() {
      if (this.s3_src_photo.length > 0) {
        this.imgNotfound = false;
        var formData = new FormData();
        // console.log('prepare to caseProperties ', this.caseProperties);
        formData.append("reqType", "sourceProduct");
        formData.append("caseID", this.caseProperties.caseID);
        formData.append("userID", this.userProperties.userID);
        // formData.append('image', this.s3_src_photo[0])
        for (let index = 0; index < this.s3_src_photo.length; index++) {
          formData.append("image[]", this.s3_src_photo[index]);
        }

        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/uploadImages.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json"
            },
            body: formData
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes, ' TTTT: ', dataRes[dataRes.length-1])
            let stillAlive = false;
            for (let index = 0; index < dataRes.length - 1; index++) {
              if (dataRes[index]["status"] != "success") {
                stillAlive = false;
                break;
              } else {
                stillAlive = true;
              }
            }
            if (stillAlive) {
              let splitImgPath = dataRes[dataRes.length - 1].split(",");
              splitImgPath.pop();
              this.src_photo_show = splitImgPath;
              this.imgStatus = true;
            } else {
              this.imgStatus = false;
            }
          })
          .catch(err => {
            console.log("ERR.upload.img.adsSource: ", err);
          });
      } else {
        this.imgNotfound = true;
      }
    }
  },
  created() {
    this.userProperties = this.$store.getters.getUserProperties;
    this.caseProperties = this.$store.getters.getCaseProperties;
  },
  mounted() {
    this.fetchSourceType();
    this.fetchMountProvince();
  }
};
</script>

